import React, {useEffect} from 'react';
import {Container, Grid} from '@material-ui/core';
import MySearchBar from '../components/inputs/MySearchBar';
import TeacherCommentsList from '../components/teacher/TeacherCommentsList';
import TeacherProfileLink from '../components/teacher/TeacherProfileLink';
import FETCH from '../fetchMethods/FETCH';
import { TeacherOptionT } from '../types/teacher-type';

export default function TeacherSearcher (){

    const [teacherID, setTeacherID] = React.useState<string>();
    const [teachers, setTeachers] = React.useState<TeacherOptionT[]>();
    

    async function getTeachers(){
        const res = await FETCH({method:'GET', path:'user/school/teachers'})
        setTeachers(res);
    }

    useEffect(() => {
        //GET TEACHERS
        getTeachers();
    }, [])

    function catchTeacher(e: any, v: TeacherOptionT){
        if(v){
            setTeacherID(v._id);
        }else{
            setTeacherID(undefined);
        }
    }

    return(
        <Container>
            <Grid container spacing={2}>
                <Grid item xs={12} >
                    {teachers ? <MySearchBar options={teachers} placeholder='Maestros' onChange={catchTeacher} optionNameField='teacherName' /> : null}
                </Grid>
                {teacherID ? 
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <TeacherProfileLink teacherID={teacherID}/>
                        </Grid>
                        <Grid item xs={12}>
                            <TeacherCommentsList userComments={false} teacherID={teacherID}/>
                        </Grid>
                    </Grid>
                    :null
                }
            </Grid>
        </Container>
    )
    
}
