import React from 'react';
import {Formik, Form} from 'formik'
import MyTextInput from '../inputs/MyTextInput';
import MySubmit from '../inputs/MySubmit';
import {createCareerS} from '../../yup-schemas/career-schema';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type CareerType = {
    careerName: string,
}

type props = {
    getCareers: () => void
}

function AddForm({getCareers}: props){

    const { enqueueSnackbar } = useSnackbar();

    async function handleSubmit(values: CareerType){
        const res = await FETCH({method:'POST', path: 'career', body:values});
        enqueueSnackbar(res.message);
        if(res.status===200) getCareers();
    }

    return(
        <Formik
            initialValues={{careerName: ''}}
            validationSchema={createCareerS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="Career" name="careerName"/>
                <MySubmit value="save"/>
            </Form>
        </Formik>
    )
}

export default AddForm;