import React from 'react'
import { Drawer, List } from '@material-ui/core'
import DrawerListItem from './DrawerListItem';
import SearchRoundedIcon from '@material-ui/icons/SearchRounded';
import MessageRoundedIcon from '@material-ui/icons/MessageRounded';
import SettingsIcon from '@material-ui/icons/Settings';
import PostAddIcon from '@material-ui/icons/PostAdd';

type props = {
    openDrawer: boolean,
    handleOpenDrawer: (arg0: boolean) => void
}

function MyDrawer({openDrawer, handleOpenDrawer}: props){

    const drawerList = <List>
                            <DrawerListItem handleOpenDrawer={handleOpenDrawer} to="/teacherSearcher" text="Buscador" icon={<SearchRoundedIcon/>}/>
                            <DrawerListItem handleOpenDrawer={handleOpenDrawer} to="/userComments" text="Comentarios" icon={<MessageRoundedIcon/>}/>
                            <DrawerListItem handleOpenDrawer={handleOpenDrawer} to="/gossips" text="Tendedero" icon={<PostAddIcon/>}/>
                            <DrawerListItem handleOpenDrawer={handleOpenDrawer} to="/userSettings" text="Ajustes" icon={<SettingsIcon/>}/>
                        </List>

    return(
        <Drawer
          open={openDrawer}
          onClose={() => {handleOpenDrawer(false)}}
        >
            {drawerList} 
        </Drawer>
    )
}

export default MyDrawer;