import React from 'react';
import {Formik, Form} from 'formik';
import {CreateTeacherS} from '../../yup-schemas/teacher-schema';
import MyTextInput from '../inputs/MyTextInput';
import { TeacherT, CreateTeacherT } from '../../types/teacher-type';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type props = {
    teacher: TeacherT,
    getTeachers: () => void,
}

function UpdateForm({teacher, getTeachers}: props){

    const { enqueueSnackbar } = useSnackbar();
    const idSubmit = `${teacher._id}USubmit`

    async function handleSubmit(values: CreateTeacherT){
        if(teacher.teacherName!==values.teacherName){
            const res = await FETCH({method:'PUT', path:`teacher/${teacher._id}`, body:values})
            enqueueSnackbar(res.message);
            if(res.status===200){
                getTeachers();
            }
        }
    }

    return(
        <Formik
            initialValues={{teacherName: teacher.teacherName}}
            validationSchema={CreateTeacherS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="Teacher" name="teacherName"/>
                <button id={idSubmit} hidden>ENVIAR</button>
            </Form>
        </Formik>
    )
}

export default UpdateForm;