import React from 'react';
import {Formik, Form} from 'formik'
import MyTextInput from '../inputs/MyTextInput';
import MySubmit from '../inputs/MySubmit';
import {CreateTeacherS} from '../../yup-schemas/teacher-schema';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type TeacherType = {
    teacherName: string,
}

type props = {
    getTeachers: () => void
}

function AddForm({getTeachers}: props){

    const { enqueueSnackbar } = useSnackbar();

    async function handleSubmit(values: TeacherType){
        const res = await FETCH({method:'POST', path: 'teacher', body:values});
        enqueueSnackbar(res.message);
        if(res.status === 200) getTeachers();
    }

    return(
        <Formik
            initialValues={{teacherName: ''}}
            validationSchema={CreateTeacherS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="Teacher" name="teacherName"/>
                <MySubmit value="save"/>
            </Form>
        </Formik>
    )
}

export default AddForm;