const URL = `https://soypolitecnico.org/api/gn2/v2/`;

console.log(URL);

function getToken(){
    let token = sessionStorage.getItem('token');
    if(token) return token;
    else return '';
}

type fetchType = {
    method: string,
    path: string,
    body?: Object,
}

async function FETCH ({method, path, body}: fetchType){
    console.log(URL+path);
    try{
        const res = await fetch(URL+path, {
        method: method,
        body: JSON.stringify(body),
        headers: {
            'Content-type': 'application/json',
            'Accept': 'application/json',
            'Authorization': getToken()
        }
        });
        let data;
        data = await res.json();
        return data;
    }catch(e){
        console.log(e);
        e.message = "Fetch failed"
        return e
    }
}

export default FETCH;