import React from 'react';
import {Typography, makeStyles, Theme, Slider, Box } from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
    title:{
        fontFamily: "Unica One"
    },
    sliderLabel:{
        fontSize: 17,
        fontFamily: 'Encode Sans Condensed',
        fontWeight: 'bold',
    },
    conteSlider:{
        height: 300,
        display:"flex",
        justifyContent:'center',
        fontSize: 100,
        marginTop: theme.spacing(4)
    }
}));


function TeacherEvaluation(){

    const classes = useStyle();

    return(
        <>
            <Typography className={classes.title} align="center" variant="h2" color="initial">FORMA DE EVALUAR</Typography>
            <Typography align="center" variant="subtitle1" color="initial">Gracias a tus datos, calificamos a los profesores</Typography>
            <Box className={classes.conteSlider}>
            <Slider
                color="primary"
                classes={{markLabel: classes.sliderLabel}}
                disabled
                orientation="vertical"
                max={10}
                aria-labelledby="Hola x2"
                defaultValue={[0,1,2,3,5,7,10]}
                marks={[
                    {value:0, label:"+/-0 Barco"},
                    {value:1, label:"+/-1 Respondi"},
                    {value:2, label:"+/-1 Mand docs/videos"},
                    {value:7, label:"+/-1 Fue justo"},
                    {value:4, label:"+/-2 Hizo videollamadas"},
                    {value:6, label:"+/-2 Desaparecio"},
                    {value:10, label:"+/-3 Aprendiste"}
                ]}
            />
            </Box>
        </>
    )
}

export default TeacherEvaluation;