import React, {useState, useEffect} from 'react';
import {Typography, Box, Grid, Container, Button} from '@material-ui/core';
import FETCH from '../fetchMethods/FETCH';
import {useHistory} from 'react-router-dom';
import HighlightOffIcon from '@material-ui/icons/HighlightOff';
import { UserT } from '../types/user-type';

function UserSettings(){

    const [user, setUser] = useState<UserT>();
    const history = useHistory();

    async function getUser(){
        const res = await FETCH({method:'GET', path:'user'});
        setUser(res);
    }

    useEffect(() => {
        getUser();
    },[])

    return(
        <Container>
            <Grid container spacing={2} alignItems="center">
                <Grid item xs={12} alignItems="center">
                        {user ? 
                            <Box display="flex" flexDirection="column" alignItems="center">
                                <Typography align="center" variant="overline" style={{fontSize:50}}>{user.nickName}</Typography>
                                <Typography align="center" variant="overline" style={{fontSize:20, margin:'0'}} color="textSecondary">{user.schoolName}</Typography>
                                <Typography align="center" variant="overline" style={{fontSize:20, margin:'0'}} color="textSecondary">{user.careerName}</Typography>
                                <Typography align="center" variant="overline" style={{fontSize:20, margin:'0'}} color="textSecondary">{user.email}</Typography>
                            </Box>
                        : null}

                </Grid>
                <Grid item xs={12} alignItems="center">
                    <Box display="flex" justifyContent="center">
                        <Button onClick={() => {history.push('/deleteAccount')}} variant="contained" color="primary" endIcon={<HighlightOffIcon/>}>
                            ELIMINAR CUENTA
                        </Button>
                    </Box>
                </Grid>
            </Grid>
        </Container>
    )
}

export default UserSettings