import React, {useEffect, useState} from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import TeachersTable from '../components/teacher/TeachersTable';
import AddTeacher from '../components/teacher/AddTeacher';
import FETCH from '../fetchMethods/FETCH';
import { CreateTeacherT, TeacherT } from '../types/teacher-type';
import AddRel from '../components/adminForms/AddRelForm';
import SelectData from '../components/adminForms/SelectData';
import { RelSchoolS, RelSubjectS } from '../yup-schemas/rels-schema';

function Teachers(){

    const [teachers, setTeachers] = useState<TeacherT[]>();
    const selectSchools =  <SelectData pathData='schools' name='idSchool' label="School" fieldOption="schoolName" />
    const selectSubjects =  <SelectData pathData='subjects' name='idSubject' label="Subject" fieldOption="subjectName" />

    async function getTeachers(){
        const res = await FETCH({method:'GET', path:'teachers'})
        res.sort();
        setTeachers(res);
    }

    useEffect(()=> {
        getTeachers();
    },[])

    function getRelTeachers(): CreateTeacherT[] | undefined{
        let res
        if(teachers){
            res = teachers.filter((t)=>{
                if(t.checked) return true;
                else return false;
            })
        }
        return res;
    }

    function fetchPathSubject(idTeacher: string): string{
        return `teacher/${idTeacher}/subject`;
    }

    function fetchPathSchool(idTeacher: string): string{
        return `teacher/${idTeacher}/school`;
    }

    return(
        <Container>
            <Grid container spacing={2}>
                <Grid item xs={12} md={5}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h5" color="initial">Teachers</Typography>
                            <AddTeacher getTeachers={getTeachers} />
                        </Grid>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h5" color="initial">Teachers - School</Typography>
                            <AddRel fetchPath={fetchPathSchool} initialValues={{idSchool: ''}} validationSchema={RelSchoolS} selectData={selectSchools} getRelsData={getRelTeachers}/>
                        </Grid>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h5" color="initial">Teachers - Subjects</Typography>
                            <AddRel fetchPath={fetchPathSubject} initialValues={{idSubject: ''}} validationSchema={RelSubjectS} selectData={selectSubjects} getRelsData={getRelTeachers}/>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} md={7}>
                    <TeachersTable getTeachers={getTeachers} teachers={teachers}/>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Teachers;