import React from 'react';
import {Formik, Form} from 'formik';
import {CreateSchoolS} from '../../yup-schemas/school-schema';
import MyTextInput from '../inputs/MyTextInput';
import { SchoolT, CreateSchoolT } from '../../types/school-type';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type props = {
    school: SchoolT,
    getSchools: () => void,
}

function UpdateForm({school, getSchools}: props){

    const { enqueueSnackbar } = useSnackbar();
    const idSubmit = `${school._id}USubmit`

    async function handleSubmit(values: CreateSchoolT){
        if(school.schoolName!==values.schoolName){
            const res = await FETCH({method:'PUT', path:`school/${school._id}`, body:values})
            enqueueSnackbar(res.message);
            if(res.status===200){
                getSchools();
            }
        }
    }

    return(
        <Formik
            initialValues={{schoolName: school.schoolName}}
            validationSchema={CreateSchoolS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="School" name="schoolName"/>
                <button id={idSubmit} hidden>ENVIAR</button>
            </Form>
        </Formik>
    )
}

export default UpdateForm;