import React, {useState} from 'react';
import {TableContainer, Table, TableBody, TableCell, TableRow, IconButton} from '@material-ui/core';
import { RoleTableT } from '../../types/role-type';
import FormFindUserByRole from './FormFindUserByRole';
import BlockIcon from '@material-ui/icons/Block';
import FETCH from '../../fetchMethods/FETCH';
import {useSnackbar} from 'notistack';

function UsersTable(){

    const [users, setUsers] = useState<RoleTableT[]>();
    const {enqueueSnackbar} = useSnackbar();

    async function handleBlock(idUser:string){
        const res = await FETCH({method:'PUT', path:`user/${idUser}/lock`})
        enqueueSnackbar(res.message);
    }

    return(
        <>
        <FormFindUserByRole setUsers={setUsers}/>
        <TableContainer>
            <Table size="small">
                <TableBody>
                    {users? users.map((u: RoleTableT) => (
                        <TableRow key={u._id}>
                            <TableCell>{u.nickName}</TableCell>
                            <TableCell align="right">
                                <IconButton aria-label="block User" onClick={()=>handleBlock(u._id)}>
                                    <BlockIcon/>
                                </IconButton>
                            </TableCell>
                        </TableRow>
                    )): null}
                </TableBody>
            </Table>
        </TableContainer>
        </>
    )
}

export default UsersTable;