import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom'
import FETCH from '../../fetchMethods/FETCH';
import {Button, makeStyles, Theme, createStyles} from '@material-ui/core';

const useStyles = makeStyles((theme: Theme)=>
  createStyles({
    Link:{
        textDecoration: 'none',
        color: theme.palette.grey[400],
        '&:hover':{
          color: theme.palette.common.white,
        }
    },
  })
)

function RoleLinks(){

    const [role, setRole] = useState<string>();
  
    async function getRole(){
      const res = await FETCH({method:'GET', path:'user/role'});
      if(res.status===200) {
        setRole(res.role)
      }
    }
  
    useEffect(()=>{
      getRole()
    },[])
  
    return(
      <>
        { role === 'capturer' || role === 'admin' ? <CapturerLinks/> : null}
        { role === 'admin' ? <AdminLinks/> : null}
      </>
    )
  }
  
  function AdminLinks(){
    const classes = useStyles();
    return(
      <>
        <Link to="/careers" className={classes.Link} >
          <Button color="inherit">Careers</Button>
        </Link>
        <Link to="/schools" className={classes.Link} >
          <Button color="inherit">Schools</Button>
        </Link>
        <Link to="/roles" className={classes.Link} >
          <Button color="inherit">Roles</Button>
        </Link>
      </>
    )
  }
  
  function CapturerLinks(){
  
    const classes = useStyles();
  
    return(
      <>
        <Link to="/subjects" className={classes.Link} >
          <Button color="inherit">Subjects</Button>
        </Link>
        <Link to="/teachers" className={classes.Link} >
          <Button color="inherit">Teachers</Button>
        </Link>
      </>
    )
  }

  export default RoleLinks;