import React from 'react';
import { Formik, Form } from 'formik';
import MyTextInput from '../inputs/MyTextInput';
import MySubmit from '../inputs/MySubmit';
import { SearchNickNameS } from '../../yup-schemas/user-schema';
import FETCH from '../../fetchMethods/FETCH';
import { RoleUserT } from '../../types/role-type';
import {useSnackbar} from 'notistack'

type props = {
    setUser: (arg0: RoleUserT | undefined) => void
}

function FormFindUser({setUser}: props){

    const {enqueueSnackbar} = useSnackbar();

    async function handleSubmit({nickName}: {nickName: string}){
        const res = await FETCH({method:'GET', path:`user/nickName/${nickName}/role`})
        if(res.status === 200) setUser(res.user);
        else {
            enqueueSnackbar(res.message)
            setUser(undefined);
        }
    }

    return(
        <Formik
            initialValues={{nickName: ''}}
            validationSchema={SearchNickNameS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="NickName" name="nickName"/>
                <MySubmit value="Buscar Usuario"/>
            </Form>
        </Formik>
    )
}

export default FormFindUser;