import React from 'react'
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import EmailIcon from '@material-ui/icons/Email';
import {Grid, Typography, makeStyles, Theme} from '@material-ui/core'

const useStyle = makeStyles((theme: Theme) => ({
    contactItem: {
        display:"flex",
        flexDirection:"column",
        justifyContent:"center",
        alignItems: "center"
    },
}));

function Contact(){

    const classes = useStyle();

    return(
        <Grid container spacing={3}>
            <Grid item xs={12}>
                <Typography align="center" variant="h4" color="initial">Contactanos</Typography>
                <Typography align="center" variant="body2" color="initial">
                    Necesitas ayuda? / Tienes una propuesta? / Quisieras contribuir?
                </Typography>
            </Grid>
            <Grid item xs={12}>
                <Grid container>
                    <Grid item xs={6} sm={3} className={classes.contactItem}>
                        <FacebookIcon style={{fontSize: 90}} />
                        <Typography variant="button" color="initial">Facebook</Typography>
                    </Grid>
                    <Grid item xs={6} sm={3} className={classes.contactItem}>
                        <InstagramIcon style={{fontSize: 90}} />
                        <Typography variant="button"color="initial">Instagram</Typography>
                    </Grid>
                    <Grid item xs={6} sm={3} className={classes.contactItem}>
                        <EmailIcon style={{fontSize: 90}} />
                        <Typography variant="button" color="initial">Email</Typography>
                    </Grid>
                    <Grid item xs={6} sm={3} className={classes.contactItem}>
                        <WhatsAppIcon style={{fontSize: 90}} />
                        <Typography variant="button" color="initial">WhatsApp</Typography>
                    </Grid>
                </Grid>
            </Grid>
        </Grid>
    )
}

export default Contact;