import React, {useState, ChangeEvent} from 'react'
import {TableRow, TableCell, Checkbox} from '@material-ui/core'
import OptionsCell from './OptionsCell';

type props = {
    xName: string,
    btnIdForm: string,
    deleteFunction: () => void,
    updateForm: JSX.Element,
    checked?: boolean | undefined,
    setChecked?: (e: ChangeEvent<HTMLInputElement>) => void
}


function DinamicRow({xName, btnIdForm, deleteFunction, updateForm, setChecked, checked}: props){

    const [isUpdating, setIsUpdating] = useState(false);

    return(
        <TableRow>
            {setChecked?
                <TableCell>
                    <Checkbox
                        checked={checked}
                        onChange={setChecked}
                        inputProps={{ 'aria-label': 'add to list to rel' }}
                    />
                </TableCell>
            :null}
            <TableCell>
                {!isUpdating?
                    <span>{xName}</span>
                :
                    {...updateForm}
                }
            </TableCell>
            <OptionsCell setIsUpdating={setIsUpdating} btnIdForm={btnIdForm} deleteFunction={deleteFunction}/>
        </TableRow>
    )
}

export default DinamicRow;