import React, { useState, useEffect, ChangeEvent} from 'react';

import { Table, TableContainer, TableBody } from '@material-ui/core';
import { CareerT } from '../../types/career-type';
import UpdateCareer from './UpdateCareer';
import SearchBar from '../adminForms/SearchBar';
import FETCH from '../../fetchMethods/FETCH';
import {useSnackbar} from 'notistack';
import DinamicRow from '../adminForms/DinamicRow';

type props = {
    getCareers: () => void,
    careers: CareerT[] | undefined
}

function Tabla({getCareers, careers}: props){

    //fCareers = filterCareers
    const [fCareers, setFCareers] = useState<CareerT[] | undefined>(careers);
    const {enqueueSnackbar} = useSnackbar();    

    useEffect(()=>{
        setFCareers(careers);
    },[careers])

    async function deleteCareer(idCareer: string){
        const res = await FETCH({method:'DELETE', path:`career/${idCareer}`});
        enqueueSnackbar(res.message);
        if(res.status === 200) getCareers();
    }

    return(
        <>
            <SearchBar data={careers} optionNameField="careerName" setFilteredData={setFCareers} label="Seach career"/>
            <TableContainer>
                <Table size="small">
                    <TableBody>
                        {fCareers? fCareers.map((s: CareerT) => (
                            <DinamicRow checked={s.checked} setChecked={(e:ChangeEvent<HTMLInputElement>)=>{s.checked=e.target.checked}} updateForm={<UpdateCareer getCareers={getCareers} career={s} />} deleteFunction={()=>deleteCareer(s._id)} btnIdForm={`${s._id}USubmit`} xName={s.careerName} key={s._id} />
                        )): null}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}





export default Tabla;