import React, {useState} from 'react';
import DeleteIcon from '@material-ui/icons/Delete';
import CreateIcon from '@material-ui/icons/Create';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import { TableCell, IconButton } from '@material-ui/core';

type props = {
    setIsUpdating: (arg0: boolean) => void
    deleteFunction: () => void,
    //Form Update Name
    btnIdForm: string,
}

function OptionsCell({setIsUpdating, deleteFunction, btnIdForm }: props){

    const [option, setOption] = useState<string | undefined>()

    function clickUpdateForm(){
        const btnSend =  (document.getElementById(btnIdForm) as HTMLButtonElement)
        if(btnSend) btnSend.click();
        setIsUpdating(false);
        setOption(undefined);
    }

    function handleUpdateBtn(){
        setIsUpdating(true);
        setOption('u'); 
        document.addEventListener('keydown', (e)=>{
            if(e.key === 'Enter'){
                clickUpdateForm();
            }
        });
    }
    
    function handleOption(){
        switch(option){
            case 'u': 
                clickUpdateForm();
                break;
            case 'd': 
                deleteFunction(); 
                setOption(undefined);
            break;
        }
    }
    
    return(
        <TableCell align="right">
            {!option ?
                <>
                    <IconButton aria-label="update" onClick={handleUpdateBtn}>
                        <CreateIcon/>
                    </IconButton>
                    <IconButton aria-label="delete" onClick={()=>setOption('d')}>
                        <DeleteIcon/>
                    </IconButton>
                </>
            : 
                <>
                    <IconButton aria-label="accept" onClick={handleOption}>
                        <CheckIcon/>
                    </IconButton>
                    <IconButton aria-label="decline" onClick={()=>{setIsUpdating(false);setOption(undefined);}}>
                        <CloseIcon/>
                    </IconButton>
                </>
            }
        </TableCell>
    )
}

export default OptionsCell;