import React from 'react';
import Rating from '@material-ui/lab/Rating';
import DonkeyIcon from '../../DonkeyIcon';
import {Box, Typography} from '@material-ui/core'
import FETCH from '../../fetchMethods/FETCH';

type Props = {
    name: string,
    rank: number | undefined
    idComment: string
}

export default function RatingComment({name, rank, idComment} : Props){

const [rankValue, setRankValue] = React.useState<number | undefined>(rank);

async function handleRank(rank: number | null){
    if(rank){
        await FETCH({method:'POST', path:`rankComment/${idComment}`, body:{rank}})
        setRankValue(rank);
    }
}

return(
    <>
        {rankValue || rankValue===0 ?
            <Box display="flex" alignItems="center">
                <Rating name={name} icon={<DonkeyIcon/>} value={rankValue} disabled/>
                <Box ml={1}><Typography variant="body2">{rankValue}</Typography></Box>
            </Box>
        :
            <Rating name={name} icon={<DonkeyIcon/>} value={rankValue} onChange={(e, rank) => {handleRank(rank)}}/>
        }
    </>
)
}



