import React, {useState, useEffect} from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import CareersTable from '../components/career/CareersTable';
import AddCareer from '../components/career/AddCareer';
import { CareerT } from '../types/career-type';
import FETCH from '../fetchMethods/FETCH';
import AddRel from '../components/adminForms/AddRelForm';
import SelectData from '../components/adminForms/SelectData';
import { RelSchoolS } from '../yup-schemas/rels-schema';

function Careers(){

    const [careers, setCareers] = useState<CareerT[]>();
    const selectSchools =  <SelectData pathData='schools' name='idSchool' label="School" fieldOption="schoolName" />

    async function getCareers(){
        const res = await FETCH({method:'GET', path:'careers'})
        setCareers(res);
    }

    useEffect(()=> {
        getCareers();
    },[])

    function getRelCareers(): CareerT[] | undefined{
        let res
        if(careers){
            res = careers.filter((s)=>{
                if(s.checked) return true;
                else return false;
            })
        }
        return res;
    }

    function fetchPath(idCareer: string): string{
        return `career/${idCareer}/school`;
    }

    return(
        <Container>
            <Grid container spacing={2}>
                <Grid item xs={12} md={5}>
                    <Grid container spacing={1}>
                      <Grid item xs={12}>
                        <Typography align="center" variant="h5" color="initial">Careers</Typography>
                        <AddCareer getCareers={getCareers} />
                      </Grid>
                      <Grid item xs={12}>
                            <Typography align="center" variant="h5" color="initial">Careers - Schools</Typography>
                            <AddRel fetchPath={fetchPath} initialValues={{idSchool: ''}} validationSchema={RelSchoolS} selectData={selectSchools} getRelsData={getRelCareers} />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} md={7}>
                    <CareersTable getCareers={getCareers} careers={careers} />
                </Grid>
            </Grid>
        </Container>
    )
}

export default Careers;