import React from 'react';
import {useField} from 'formik';
import { TextField} from '@material-ui/core';

type Props = {
    name:string, 
    label:string, 
}

const MyTextArea = ({name, label} : Props) => {
    const [field, meta] = useField(name);
    return(
        <>
            {meta.touched && meta.error 
                ? 
                <TextField fullWidth multiline label={label} error helperText={meta.error} {...field} rows="4" variant="outlined" size="small" />    
                : 
                <TextField fullWidth multiline label={label} {...field} rows="4" variant="outlined" size="small"/>
            }
        </>
    );
}

export default MyTextArea;