import React, {useState, useEffect} from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import SubjectsTable from '../components/subject/SubjectsTable';
import AddSubject from '../components/subject/AddSubject';
import { SubjectT } from '../types/subject-type';
import FETCH from '../fetchMethods/FETCH';
import AddRel from '../components/adminForms/AddRelForm';
import SelectData from '../components/adminForms/SelectData';
import { RelSchoolS } from '../yup-schemas/rels-schema';

function Subjects(){

    const [subjects, setSubjects] = useState<SubjectT[]>();
    const selectSchools =  <SelectData pathData='schools' name='idSchool' label="School" fieldOption="schoolName" />

    async function getSubjects(){
        const res = await FETCH({method:'GET', path:'subjects'})
        setSubjects(res);
    }

    useEffect(()=> {
        getSubjects();
    },[])

    function getRelSubjects(): SubjectT[] | undefined{
        let res
        if(subjects){
            res = subjects.filter((s)=>{
                if(s.checked) return true;
                else return false;
            })
        }
        return res;
    }

    function fetchPath(idSubject: string): string{
        return `subject/${idSubject}/school`;
    }

    return(
        <Container>
            <Grid container spacing={2}>
                <Grid item xs={12} md={5}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h5" color="initial">Subjects</Typography>
                            <AddSubject getSubjects={getSubjects} />
                        </Grid>
                        <Grid item xs={12}>
                            <Typography align="center" variant="h5" color="initial">School - Subjects</Typography>
                            <AddRel fetchPath={fetchPath} initialValues={{idSchool: ''}} validationSchema={RelSchoolS} selectData={selectSchools} getRelsData={getRelSubjects} />
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} md={7}>
                    <SubjectsTable setSubjects={setSubjects} getSubjects={getSubjects} subjects={subjects} />
                </Grid>
            </Grid>
        </Container>
    )
}

export default Subjects;