import React from 'react';
import { Link } from 'react-router-dom';
import {Button} from '@material-ui/core'
import linkStyle from '../../material-ui/linkStyle';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';

type props = {
    teacherID: string
}

function TeacherProfileLink({teacherID}: props){
    
    const linkValue = `/teacherProfile/${teacherID}`;
    const classes = linkStyle();

    return(
        <Link to={linkValue} className={classes.link}>
            <Button
              variant="text"
              color="secondary"
              startIcon={<ChevronRightIcon/>}
              size="large"
            >
              VISITAR PERFIL
            </Button>
        </Link>
    )
}

export default TeacherProfileLink;