import React from 'react';
import { Card, CardContent, CardHeader, IconButton, Tooltip, CardActions, makeStyles, Theme} from '@material-ui/core';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';import RatingComment from './RatingComment';
import {CommentCardT} from '../../types/comment-type';
import DeleteCommentButton from './DeleteCommentButton';
import ToolTipInfo from './ToolTipInfo';
import CommentText from './CommentText';

interface Props {
    background: string
}

const useStyle = makeStyles((theme: Theme) => ({
    card:{
        borderRadius: theme.spacing(2,0,0,0),
        padding: theme.spacing(0)
    },
    actionsCardHeader:{
        margin: theme.spacing(0)
    },
    commentCardHeader:{
        color: theme.palette.common.white,
        background: (props: Props) => props.background,
        paddingTop: theme.spacing(0),
        paddingBottom: theme.spacing(0)
    },
    iconWhite:{
        color: theme.palette.common.white
    },
    cardActions:{
        display: "flex",
        justifyContent: "flex-end"
    },
}));

interface commentType extends CommentCardT {
    getUserComments?: () => void,
    color: string,
}

export default function CommentCard(prop: commentType){
    const {color, comment, subject, user, createdAt, currentUser, _id, rank, getUserComments} = prop;

    const classes = useStyle({
        background: color
    });
    
    return(
        
            <Card raised className={classes.card}>
                <CardHeader className={classes.commentCardHeader}
                    classes={{action: classes.actionsCardHeader}}
                    title={subject.subjectName}
                    titleTypographyProps = {{"variant":"overline"}}
                    action={
                        <IconButton aria-label="info">
                            <Tooltip enterTouchDelay={0} placement="bottom-end" title={<ToolTipInfo user={user.nickName} createdAt={createdAt} />}>
                                <InfoOutlinedIcon className={classes.iconWhite}/>
                            </Tooltip>
                        </IconButton>
                    }
                />
                <CardContent>
                    <CommentText comment={comment} />
                </CardContent>
                <CardActions className={classes.cardActions}>
                    {currentUser === user._id && getUserComments ? 
                        <DeleteCommentButton idComment={_id} getUserComments={getUserComments} />
                        : null
                    }
                    <RatingComment name={_id} rank={rank} idComment={_id}/>
                </CardActions>
            </Card>
    )
}

