import React, { useState } from 'react';
import { Fab, makeStyles, Theme, createStyles, Container, Divider } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import {Link} from 'react-router-dom';
import TeacherCommentsList from '../components/teacher/TeacherCommentsList';
import TotalQualificationsText from '../components/user/TotalQualificationsText';

const useStyles = makeStyles((theme: Theme)=>
  createStyles({
    fab:{
        position: 'fixed',
        bottom: theme.spacing(2),
        right: theme.spacing(2)
    },
    divider:{
        margin: theme.spacing(2, 0, 3, 0)
    }
  })
)

export default function UserComments(){
    
    const classes = useStyles();
    const [totalComments, setTotalComments] = useState<number>();
    
    return(
        <>
            <Container>

                <TotalQualificationsText totalComments={totalComments}/>

                <Divider variant="middle" className={classes.divider} />

                <TeacherCommentsList userComments={true} setTotalComments={setTotalComments} />
                <Link to="qualificationOnlineForm">
                    <Fab color="primary" aria-label="add qualification button" className={classes.fab}>
                        <Add />
                    </Fab>
                </Link>
                
            </Container>
        </>
    )
}