import React from 'react';
import {Typography, makeStyles, Theme, Box } from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
    textGradient: {
        background: `linear-gradient(90deg, ${theme.palette.secondary.light} 0%, ${theme.palette.secondary.dark} 100%)`,
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
        fontWeight: 'bold',
        fontSize: 50,
        marginTop: theme.spacing(10),
        marginBottom: theme.spacing(3),
    },
    txtDecalogo:{
        textAlign:'center',
        fontFamily: 'Encode Sans Condensed'
    }
}));

function Decalogue(){

    const classes = useStyle();

    return(
        <>
            <Box display="flex" flexDirection="column" alignItems="center">
                <Typography className={classes.txtDecalogo} style={{fontSize:20}} color="initial">PORQUE ASPIRO A SER TODO UN HOMBRE</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:16, fontWeight:"bold"}} color="initial">PORQUE EXIJO MIS DEBERES ANTES QUE MIS DERECHOS</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:18}} color="initial">POR CONVICCIN Y NO POR CIRCUNSTANCIA</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:15}} color="initial">PARA ALCANZAR LAS CONQUISTAS UNIVERSALES Y OFRECERLAS A MI PUEBLO</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:16, fontWeight:"bold"}} color="initial">POR QUE ME DUELE LA PATRIA EN MIS ENTRAAS Y ASPIRO A CALMAR SUS DOLENCIAS</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:15}} color="initial">PORQUE ARDO EN DESEOS DE DESPERTAR AL HERMANO DORMIDO</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:20}} color="initial">PARA PRENDER UNA ANTORCHA EN EL ALTAR DE LA PATRIA</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:16, fontWeight:"bold"}}  color="initial">PORQUE ME DIGNIFICO Y SIENTO EL DEBER DE DIGNIFICAR A MI INSTITUCION</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:19}} color="initial">PORQUE MI RESPETADA LIBERTAD DE JOVEN Y ESTUDIANTE ME IMPONE LA RAZN DE RESPECTAR ESTE RECINTO</Typography>
                <Typography className={classes.txtDecalogo} style={{fontSize:17, fontWeight:"bold"}} color="initial">POR QUE TRADUZCO LA TRICOTOMA DE MI BANDERA COMO TRABAJO, DEBER Y HONOR</Typography>
            </Box>
             
        </>
    )
}

export default Decalogue;