import React from 'react';
import {Formik, Form} from 'formik';
import SelectData from '../adminForms/SelectData';
import {Button, makeStyles, Theme } from '@material-ui/core';
import { AddRoleS } from '../../yup-schemas/user-schema';
import FETCH from '../../fetchMethods/FETCH';
import { RoleTableT } from '../../types/role-type';

type props = {
    setUsers: (arg0: RoleTableT[] | undefined) => void
}

const useStyle = makeStyles((theme: Theme) => ({
    submit: {
        margin: theme.spacing(3,0,3,0),
        borderRadius: theme.spacing(5),
        color: theme.palette.common.white,
        background: `linear-gradient(90deg, ${theme.palette.primary.dark} 0%, ${theme.palette.primary.light} 100%)`,
    },
}));

function FormFindUserByRole({setUsers}: props){

    const classes = useStyle();

    async function handleSubmit({idRole}: {idRole: string}){
        const res = await FETCH({method: 'GET', path: `users/role/${idRole}`});
        setUsers(res);
    }

    return(
        <Formik
            initialValues={{idRole:''}}
            onSubmit= {(values)=>{
                handleSubmit(values);
            }}
            validationSchema={AddRoleS}
        >
            <Form>
                <SelectData pathData='roles' name='idRole' label='Role' fieldOption= 'name' />
                <Button className={classes.submit} fullWidth type="submit" variant="outlined" color="primary">
                    Buscar usuarios
                </Button>
            </Form>
        </Formik>
    )
}
export default FormFindUserByRole;