import React, {useEffect, useState} from 'react';
import FETCH from '../../fetchMethods/FETCH';
import MySelect from '../inputs/MySelect';

type props = {
    pathData: string,
    name:string,
    label:string,
    fieldOption: string
}
type optionType = {
    _id: string
    [fieldOption:string]: string,
}

function SelectData({pathData, name, label, fieldOption}: props){

    const [subjects, setSubjects] = useState<optionType[]>()

    async function getSubjects(){
        const res = await FETCH({method: 'GET', path:pathData});
        setSubjects(res);
    }

    useEffect(()=>{
        getSubjects();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    return(
        <MySelect id={name} name={name} label={label}>
            {subjects? subjects.map((s) => (
                <option key={s._id} value={s._id}>{s[fieldOption]}</option>
            )) : null}
        </MySelect>
    )
}

export default SelectData;