import React from 'react';
import Routes from './routes/Routes';
import {Toolbar} from '@material-ui/core';
import { HashRouter as Router } from 'react-router-dom';
import Navigation from './components/navigation/Navigation';
import ScrollTopFab from './components/navigation/ScrollTopFab';

function App(){

  const [isAuthenticated, setIsAuthenticated] = React.useState<boolean>(sessionStorage.getItem('token')? true : false);

  return(
    <Router>
        <div>
            
            <Navigation isAuthenticated={isAuthenticated} setIsAuthenticated={setIsAuthenticated}/>

            <Toolbar id="back-to-top-anchor" style={{marginBottom: '20px'}} />

            <ScrollTopFab/>

            <Routes setIsAuthenticated={setIsAuthenticated} />
        </div>
    </Router>
  )
}

export default App;