import React from 'react';
import {TextField, makeStyles, Theme} from '@material-ui/core';
import {Autocomplete} from '@material-ui/lab';

const useStyle = makeStyles((theme: Theme) => ({
  search: {
      margin: theme.spacing(0)
  },
}));

type OptionType = {
  _id: string, 
  [optionNameField:string]:string
}

type Props = {
    options: OptionType[]
    placeholder: string
    onChange: any
    optionNameField: string
}

const MySearchBar = ({options, placeholder, onChange, optionNameField} : Props ) => {
  const classes = useStyle();
  return(
      <Autocomplete
        freeSolo
        options={[...options]}
        getOptionLabel={opcion => opcion[optionNameField]}
        onChange={onChange}
        renderInput={(params) => (
          <TextField
            {...params}
            placeholder={placeholder}
            className={classes.search}
            margin="normal"
            id="My"
            variant="outlined"
            InputProps={{ ...params.InputProps}}
          />
        )}
      />
  )
}


export default MySearchBar;