import React from 'react';
import { Switch } from 'react-router-dom';
import Home from '../pages/Home';
import SignIn from '../pages/SignIn';
import SignUp from '../pages/SignUp';
import QualificationOnlineForm from '../pages/QualificationOnlineForm';
import TeacherSearcher from '../pages/TeacherSearcher';
import UserComments from '../pages/UserComments';
import TeacherProfile from '../pages/TeacherProfile';
import PublicRoute from './PublicRoute';
import ProtectedRoute from './ProtectedRoute';
import AddTeacher from '../pages/AddTeacher';
import ResetPassword from '../pages/ResetPassword';
import NotFound from '../pages/NotFound';
import EmailResetPassword from '../pages/EmailResetPassword';
import UserSettings from '../pages/UserSettings';
import DeleteAccount from '../pages/DeleteAccount';
import Info from '../pages/Info';
import Gossips from '../pages/Gossips';
import GossipForm from '../pages/GossipForm';
import Subjects from '../pages/Subjects';
import Teachers from '../pages/Teachers';
import Schools from '../pages/Schools';
import Careers from '../pages/Careers';
import Roles from '../pages/Roles';

type props = {
    setIsAuthenticated: (arg0: boolean) => void
}

function Routes({setIsAuthenticated}: props){

  return(
    
    <Switch>

        {/*Rutas Publicas*/}
        <PublicRoute exact={true} path="/">
            <Home />
        </PublicRoute>
        <PublicRoute exact={true} path="/signIn">
            <SignIn setIsAuthenticated={setIsAuthenticated} />
        </PublicRoute>
        <PublicRoute exact={true} path="/signUp">
            <SignUp />
        </PublicRoute>
        <PublicRoute exact={true} path="/resetPassword/:idUser">
            <ResetPassword setIsAuthenticated={setIsAuthenticated}/>
        </PublicRoute>
        <PublicRoute exact={true} path="/resetPassword">
            <EmailResetPassword/>
        </PublicRoute>
        <PublicRoute exact={true} path="/info">
            <Info/>
        </PublicRoute>

        {/*Rutas Privadas*/}

        {/*User*/}
        <ProtectedRoute exact={true} path="/qualificationOnlineForm">
            <QualificationOnlineForm/>
        </ProtectedRoute>        
        <ProtectedRoute exact={true} path="/teacherSearcher">
            <TeacherSearcher/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/teacherProfile/:idTeacher">
            <TeacherProfile/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/gossips">
            <Gossips/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/userSettings">
            <UserSettings/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/deleteAccount">
            <DeleteAccount setIsAuthenticated={setIsAuthenticated}/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/userComments">
            <UserComments/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/addTeacher">
            <AddTeacher/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/addGossip">
            <GossipForm/>
        </ProtectedRoute>

        {/*Admin*/}
        <ProtectedRoute exact={true} path="/subjects">
            <Subjects/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/teachers">
            <Teachers/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/schools">
            <Schools/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/careers">
            <Careers/>
        </ProtectedRoute>
        <ProtectedRoute exact={true} path="/roles">
            <Roles/>
        </ProtectedRoute>

        {/* Not found 404 page */}
        <PublicRoute exact={true} path="*">
            <NotFound/>
        </PublicRoute>

    </Switch>
  )
}

export default Routes;