import React from 'react';
import {Formik, Form} from 'formik'
import MyTextInput from '../inputs/MyTextInput';
import MySubmit from '../inputs/MySubmit';
import {CreateSubjectS} from '../../yup-schemas/subject-schema';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type SubjectType = {
    subjectName: string,
}

type props = {
    getSubjects: () => void
}

function AddForm({getSubjects}: props){

    const { enqueueSnackbar } = useSnackbar();

    async function handleSubmit(values: SubjectType){
        const res = await FETCH({method:'POST', path: 'subject', body:values});
        enqueueSnackbar(res.message);
        if(res.status===200) getSubjects();
    }

    return(
        <Formik
            initialValues={{subjectName: ''}}
            validationSchema={CreateSubjectS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="Subject" name="subjectName"/>
                <MySubmit value="save"/>
            </Form>
        </Formik>
    )
}

export default AddForm;