import React from 'react';
import {ListItem, ListItemIcon, ListItemText} from '@material-ui/core';
import {Link} from 'react-router-dom'
import linkStyle from '../../material-ui/linkStyle';

type props = {
    text: string,
    to: string,
    icon: any,
    handleOpenDrawer: (arg0: boolean) => void
}

function DrawerListItem({text, to, icon, handleOpenDrawer} : props){
    
    const classes = linkStyle();

    return(
        <Link className={classes.link} to={to} >
            <ListItem button key={text} onClick={() => {handleOpenDrawer(false)}}>
                <ListItemIcon>
                {icon}
                </ListItemIcon>
                <ListItemText primary={text}/>
            </ListItem>
        </Link>
    )
}

export default DrawerListItem;