import React from 'react';
import {Button} from '@material-ui/core';
import { RoleUserT } from '../../types/role-type';
import { Formik, Form } from 'formik';
import SelectData from '../adminForms/SelectData';
import { AddRoleS } from '../../yup-schemas/user-schema';
import FETCH from '../../fetchMethods/FETCH';
import {useSnackbar} from 'notistack';

type props = {
    user: RoleUserT
}

function FormAddRole({user}:props){

    const {enqueueSnackbar} = useSnackbar();

    async function handleSubmit({idRole}: {idRole: string}){
        const res = await FETCH({method:'PUT', path:`user/${user._id}/role/${idRole}`})
        enqueueSnackbar(res.message);
    }

    return(
        <Formik
            initialValues={{idRole:''}}
            onSubmit= {(values)=>{
                handleSubmit(values);
            }}
            validationSchema={AddRoleS}
        >
            <Form>
                <SelectData pathData='roles' name='idRole' label='Role' fieldOption= 'name' />
                <Button fullWidth type="submit" variant="outlined" color="secondary">
                    Agregar Rol
                </Button>
            </Form>
        </Formik>
    )
}

export default FormAddRole;