import { Typography, Accordion, AccordionSummary, AccordionDetails, makeStyles, Theme, createStyles } from '@material-ui/core';
import React from 'react';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

type props = {
    teacherName: string,
    gossip: string
}

const useStyles = makeStyles((theme: Theme)=>
  createStyles({
    accordion:{
        background: theme.palette.grey[200],
    }
  })
)

function GossipAccordion({teacherName, gossip}:props){

    const classes = useStyles();

    return(
        <Accordion className={classes.accordion} >
            <AccordionSummary
                expandIcon={<ExpandMoreIcon/>}
            >
                <Typography variant="button" color="initial">{teacherName}</Typography>
            </AccordionSummary>
            <AccordionDetails>
                <Typography variant="body2" color="initial">{gossip}</Typography>
            </AccordionDetails>
        </Accordion>
    )
}

export default GossipAccordion;