import React, { useState } from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import FormFindUser from '../components/roles/FormFindUser';
import { RoleUserT } from '../types/role-type';
import FormAddRole from '../components/roles/FormAddRole';
import UsersTable from '../components/roles/UsersTable';

function Roles(){

    const [user, setUser] = useState<RoleUserT>();

    return(
        <Container>
            <Grid container spacing={2}>
                <Grid item xs={12} md={6} >
                    <Typography align="center" variant="h5" color="initial">Users</Typography>
                    <FormFindUser setUser={setUser}/>
                    {user? <FormAddRole user={user} /> :null}
                </Grid>
                <Grid item xs={12} md={6} >
                    <UsersTable/>
                </Grid>
            </Grid>
        </Container>
    )
}

export default Roles;