import React, { useEffect, useState } from 'react';
import { HorizontalBar } from 'react-chartjs-2';
import FETCH from '../../fetchMethods/FETCH';

//Return and array of hsl colores between hsl(283,60,70) and hsl(360, 60, 70)
//Obviously the common light is 70 but you can change
function generateColors(totalColors : number, light : number = 70): string[]{
  const increase = (360-283) / (totalColors-1);
  let backgroundColors = [];
  let hueStart=283;
  for(let i=0; i<totalColors; i++){
    let hue = (increase*i)+hueStart;
    
    backgroundColors.push(`hsl(${hue},60%,${light}%)`)
  }
  return backgroundColors;
}

function generateColorsGradients(canvas: HTMLCanvasElement): CanvasGradient[] | []{
  const ctx = canvas.getContext("2d");

  const increase = (360-283) / (6);
  let backgroundColors = [];
  let hueStart=283;
  for(let i=0; i<7; i++){
    let hue = (increase*i)+hueStart;
    let color1 = `hsl(${hue},60%,85%)`;
    let color2 = `hsl(${hue},60%,45%)`;
    
    if(ctx){
      const gradient = ctx.createLinearGradient(0,0,500,0);
      gradient.addColorStop(0, color1);
      gradient.addColorStop(1, color2);
      backgroundColors.push(gradient);
    }
  }
  return backgroundColors;
}

type props = {
  idTeacher : string
}

function ChartTeacherQualifications({idTeacher}: props) {

  const borderColors = generateColors(7, 40);
  const dataLabels: string[] = ['Es Barco', 'Responde Preguntas', 'Manda Docs/Videos', 'Hace Videollamadas', 'Desaparece', 'Es Justo', '% Que Aprendieron'];
  const [dataValues, setDataValues] = useState<number[]>();

  async function getChartData(){
    const res = await FETCH({method: 'GET', path:`teacherChart/${idTeacher}`})
    setDataValues([
      res.isEasy,
      res.answer,
      res.sentDocuments,
      res.madeVideocalls,
      res.dissapear,
      res.beFair,
      res.learn
    ])
  }

  useEffect(()=> {
    getChartData();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  },[])

  const data = (canvas: HTMLCanvasElement) => {
    return{
      labels: dataLabels,
      datasets: [
        {
          label: 'Experiencia de los estudiantes en %',
          backgroundColor: generateColorsGradients(canvas),
          borderColor: borderColors,
          hoverBorderColor: borderColors,
          borderWidth: 0.5,
          data: dataValues,
        }
      ]
    }
  };

  const options = {
  
    responsive: true, 
    scales: {
      xAxes: [{
        ticks: {
          beginAtZero: true,
          max: 100
        }
      }]
    },
    legend: {
      display: false
    },
    title:{
      display: true,
      text: 'EXPERIENCIA DE LOS ESTUDIANTES EN  %'
    }
  }

  return (
      <HorizontalBar data={data} options={options}/>
  );
}

export default ChartTeacherQualifications;
