import React from 'react';
import { Formik, Form } from 'formik';
import MySubmit from '../inputs/MySubmit';
import { useSnackbar } from 'notistack';
import FETCH from '../../fetchMethods/FETCH';

type props = {
    getRelsData: Function,
    selectData: JSX.Element,
    initialValues: object,
    validationSchema: any,
    fetchPath: (arg0: string) => string,
}

function AddRel({getRelsData, selectData, initialValues, validationSchema, fetchPath}:props){

    const {enqueueSnackbar} = useSnackbar();

    function handleSubmit(values : any){
        const rels = getRelsData();
        if(rels && rels.length!==0){
            rels.forEach(async(r:any, i:number) => {
                const res = await FETCH({method:'POST', path:fetchPath(r._id), body:values});
                enqueueSnackbar(res.message);
            });
        }else{
            enqueueSnackbar('An no seleccionas nada')
        }
    }

    return(
        <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                {selectData}
                <MySubmit value="Link"/>
            </Form>
        </Formik>
    )
}

export default AddRel;