import React from 'react';
import { Typography, Button } from '@material-ui/core';

type props = {
    comment: string
}

function CommentText({comment}: props){

    //CommentText guarda lo que es visible del comentario
    const [commentText, setCommentText] = React.useState(comment.substr(0, 300));

    function readMore(){
        setCommentText(comment);
    }

    return(
        <Typography align="justify" variant="body2" gutterBottom> 
            {commentText}
            {comment.length>300 && commentText.length<=300 ? <Button color="inherit" size="small" onClick={readMore}>Seguir Leyendo </Button> : null}
        </Typography>
    )
}

export default CommentText;