import React, {useState, useEffect} from 'react';
import { Typography } from '@material-ui/core';
import FETCH from '../../fetchMethods/FETCH';

type props = {
    totalComments: number | undefined
}

function TotalQualificationsText({totalComments}: props){

    const [total, setTotal] = useState<number>(0);

    async function getTotal(){
        const res = await FETCH({method:'GET', path:'user/totalQualifications'})
        console.log(res.total);
        console.log(!totalComments);
        setTotal(res.total);
    }

    useEffect(()=> {
        getTotal();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[totalComments])

    return(
        <>
        {!totalComments && total===0 ?
            <Typography align="right" style={{width: '100%'}} variant="subtitle2">Comienza a comentar...!</Typography>
        :   <Typography align="right" style={{width: '100%'}} variant="subtitle2">Haz contribuido con {total} calificacion(es)</Typography>
        }
        </>
    )
}

export default TotalQualificationsText;