import React, { useState } from 'react';
import { Container, Grid, Typography, Backdrop, CircularProgress } from '@material-ui/core';
import {Formik, Form} from 'formik';
import MySubmit from '../components/inputs/MySubmit';
import MyEmailInput from '../components/inputs/MyEmailInput';
import formStyles from '../material-ui/formStyles';
import {ResetPWDEmailS} from '../yup-schemas/resetPassword-schema';
import FETCH from '../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type ResetPWDEmailType = {
    email: string
}

function EmailResetPassword(){

    const formClasses = formStyles();
    const [openBackdrop, setOpenBackdrop] = useState<boolean>(false);
    const { enqueueSnackbar } = useSnackbar();

    async function handleSubmit(values: ResetPWDEmailType){
        setOpenBackdrop(true);
        const res = await FETCH({method:'POST', path:`user/resetPassword/email`, body:values});
        setOpenBackdrop(false);
        enqueueSnackbar(res.message);
    }

    return(
        <>
            <Backdrop style={{zIndex: 1}} open={openBackdrop}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <Container maxWidth="xs">
                <Grid container className={formClasses.containerForm}>
                    <Typography variant="h5" color="initial">Recuperar Contrasea</Typography>
                    <Grid item xs={12}>
                        <Formik
                            initialValues = {{email: ''}}
                            validationSchema={ResetPWDEmailS}
                            onSubmit = {(values)=>{
                                handleSubmit(values);
                            }}
                        >
                            <Form className={formClasses.form} >
                                <MyEmailInput label="Correo electronico" name="email" />
                                <MySubmit value="Enviar" />
                            </Form>
                        </Formik>
                    </Grid>
                </Grid>
            </Container>
        </>
    )
}

export default EmailResetPassword;