import React, {useEffect, useState} from 'react';
import MySelect from './inputs/MySelect';
import FETCH from '../fetchMethods/FETCH';
import { TeacherT } from '../types/teacher-type';

type props = {
    setIDTeacher?: (arg0:string) => void
}

function SelectTeachers({setIDTeacher}: props){

    const [teachers, setTeachers] = useState<TeacherT[]>()

    async function getTeachers(){
        const res = await FETCH({method:'GET', path:`user/school/teachers`});
        setTeachers(res);
    }

    useEffect(() => {
        getTeachers();
    },[])

    return(
        <MySelect id="teacher" name="idTeacher" label="Maestro" onChange={setIDTeacher}>
            {teachers? teachers.map((t) => (
                <option key={t._id} value={t._id}>{t.teacherName}</option>
            )) : null}
        </MySelect>
    )
}

export default SelectTeachers;