import { createMuiTheme } from '@material-ui/core/styles'

const theme = createMuiTheme({
    palette: {
        primary: {
            main: '#212121',
            light: '#484848',
            dark: '#000000'
        },
        secondary: {
            main: 'hsl(321.5,60%,50%)',
            light: 'hsl(360,60%,50%)',
            dark: 'hsl(283,60%,50%)'
        },
    },
    overrides:{
        MuiCssBaseline:{
            '@global':{
                "*::-webkit-scrollbar": {
                    width: "10px",
                },
                "*::-webkit-scrollbar-thumb": {
                    background: `linear-gradient(0deg, hsl(360,60%,30%) 0%, hsl(283,60%,30%) 100%)`,
                    borderRadius: '5px',
                },
                "*::-webkit-scrollbar-thumb:hover": {
                    background: "#000000"
                }
            }
        }
    }
  });

export default theme;