import React, {useEffect, useState} from 'react';
import {Badge, makeStyles, Theme} from '@material-ui/core';
import FETCH from '../../fetchMethods/FETCH';
import DonkeyIcon from '../../DonkeyIcon';

const useStyles = makeStyles((theme : Theme) => ({
    yellowBadge: {
        backgroundColor: "hsl(60, 60%, 50%)",
    },
    redBadge: {
        backgroundColor: "hsl(0, 60%, 50%)",
    },
    greenBadge: {
        backgroundColor: "hsl(150, 60%, 50%)",
    }
}))

type props = {
    idTeacher: string
}

function TeacherAvatarScored({idTeacher}: props) {

    const classes = useStyles();
    const [score, setScore] = useState<number>(0);

    async function getTeacherScore(){
        const res = await FETCH({method:'GET', path:`teacherScore/${idTeacher}`})
        setScore(res.score);
    }

    useEffect(()=> {
        getTeacherScore();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return(
        <Badge classes={{badge: classes.greenBadge}} overlap='circle' badgeContent={score} showZero color='primary' >
            <DonkeyIcon style={{ fontSize: 80 }} />
        </Badge>
    )
}

export default TeacherAvatarScored;