import React from 'react'
import { Toolbar, IconButton, Typography, createStyles, makeStyles, Theme, Button } from '@material-ui/core';
import AppBar from "@material-ui/core/AppBar";
import { Link } from 'react-router-dom';
import MenuRoundedIcon from '@material-ui/icons/MenuRounded';
import LogOutButton from '../user/LogOutButton';
import NickNameText from '../user/NickNameText';
import RoleLinks from './RoleLinks';

const useStyles = makeStyles((theme: Theme)=>
  createStyles({
    title: {
        flexGrow: 1,
        textDecoration: 'none',
        color: theme.palette.common.white
    },
    appBar: {
        position: "fixed",      
    },
    menuButton:{
        marginRight: theme.spacing(2),
    },
    Link:{
        textDecoration: 'none',
        color: theme.palette.grey[400],
        '&:hover':{
          color: theme.palette.common.white,
        }
    },
  })
)

type props = {
    isAuthenticated: boolean,
    setIsAuthenticated: (arg0: boolean) => void,
    handleOpenDrawer: (arg0: boolean) => void
}

function MyAppBar({isAuthenticated, setIsAuthenticated, handleOpenDrawer}: props){

    const classes = useStyles();

    const appBarLinks = <>
                          <Link to="/info" className={classes.Link} >
                            <Button color="inherit">Info</Button>
                          </Link>
                        </>
    const appBarLinksAuthFalse = <>
                                    <Link to="/signIn" className={classes.Link} >
                                      <Button color="inherit">Sign In</Button>
                                    </Link>
                                    <Link to="/signUp" className={classes.Link}>
                                      <Button color="inherit">Sign Up</Button>
                                    </Link>
                                  </>
    const appBarLinksAuthTrue = <>
                                  <RoleLinks/>
                                  <Link to="/teacherSearcher" className={classes.Link} >
                                    <NickNameText/>
                                  </Link>
                                  <Link to="/" className={classes.Link} >
                                    <LogOutButton setIsAuthenticated={setIsAuthenticated}/>
                                  </Link>
                                </>

    return(
        <AppBar className={classes.appBar}>
          <Toolbar>
            { isAuthenticated ? 
              <IconButton 
                onClick={() => {handleOpenDrawer(true)}}
                className={classes.menuButton}
                edge="start" 
                color="inherit" 
                aria-label="menu"
              >
                <MenuRoundedIcon />
              </IconButton>
            : null}
            <Link to="/" className={classes.title}>
              <Typography variant="h6" noWrap>SOY POLITECNICO</Typography>
            </Link>
            {appBarLinks}
            { isAuthenticated ? appBarLinksAuthTrue : appBarLinksAuthFalse }
          </Toolbar>
        </AppBar>
    )
}

export default MyAppBar;