import React from 'react';
import {Formik, Form} from 'formik';
import {CreateSubjectS} from '../../yup-schemas/subject-schema';
import MyTextInput from '../inputs/MyTextInput';
import { SubjectT, CreateSubjectT } from '../../types/subject-type';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type props = {
    subject: SubjectT,
    getSubjects: () => void,
}

function UpdateForm({subject, getSubjects}: props){

    const { enqueueSnackbar } = useSnackbar();
    const idSubmit = `${subject._id}USubmit`

    async function handleSubmit(values: CreateSubjectT){
        if(subject.subjectName!==values.subjectName){
            const res = await FETCH({method:'PUT', path:`subject/${subject._id}`, body:values})
            enqueueSnackbar(res.message);
            if(res.status===200){
                getSubjects();
            }
        }
    }

    return(
        <Formik
            initialValues={{subjectName: subject.subjectName}}
            validationSchema={CreateSubjectS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="Subject" name="subjectName"/>
                <button id={idSubmit} hidden>ENVIAR</button>
            </Form>
        </Formik>
    )
}

export default UpdateForm;