import React from 'react';
import { Formik, Form } from 'formik';
import { createGossipS } from '../yup-schemas/gossip-schema';
import MySubmit from '../components/inputs/MySubmit';
import {Container, Typography, Grid, Box} from '@material-ui/core';
import SelectTeachers from '../components/SelectTeachers';
import MyTextArea from '../components/inputs/MyTextArea';
import formStyles from '../material-ui/formStyles';
import {useSnackbar} from 'notistack'
import {useHistory} from 'react-router-dom'
import FETCH from '../fetchMethods/FETCH';
import { CreateGossipT } from '../types/gossip-type';

function GossipForm(){

    const formClasses = formStyles();
    const history = useHistory();
    const {enqueueSnackbar} = useSnackbar();

    async function handleSubmit(values: CreateGossipT){
        const res = await FETCH({method: 'POST', path:'gossip', body:values});
        enqueueSnackbar(res.message);
        if(res.status === 200) history.push('gossips');
    }

    return(
        <Container component="main" maxWidth="xs">
            <Grid container className={formClasses.containerForm} >
                <Typography variant="h5" color="initial">Tendedero</Typography> 
                <Grid item xs={12}>
                    <Formik
                        initialValues={{idTeacher:'', gossip:''}}
                        validationSchema={createGossipS}
                        onSubmit={(values)=>{
                            handleSubmit(values);
                        }}
                    >
                        <Form className={formClasses.form}>
                            <SelectTeachers/>
                            <Box mt={2}>
                                <MyTextArea label="Escribe aqu..." name="gossip"/>
                            </Box>
                            <MySubmit value="Enviar"/>
                        </Form>
                    </Formik>
                    <Box my={2} >
                        <Typography color="textSecondary" align="center" style={{width: '100%'}} variant="body2">RECUERDA</Typography>
                        <Typography color="textSecondary" align="center" style={{width: '100%'}} variant="body2">Estos datos son anonimos</Typography>
                        <Typography color="textSecondary" align="center" style={{width: '100%'}} variant="body2">Al guardarlos no podras borrarlos o modificarlos</Typography>
                    </Box>
                </Grid>
            </Grid>
        </Container>
        
    )
}

export default GossipForm;