import React from 'react';
import MyPasswordInput from '../components/inputs/MyPasswordInput';
import MyEmailInput from '../components/inputs/MyEmailInput';
import MySubmit from '../components/inputs/MySubmit';
import {Grid, Typography, Container, Box} from '@material-ui/core'
import {Formik, Form} from 'formik';
import { useHistory, Link } from 'react-router-dom';
import {SignInUserS} from '../yup-schemas/user-schema';
import { useSnackbar } from 'notistack';
import formStyles from '../material-ui/formStyles';
import LinkStyle from '../material-ui/linkStyle';
import FETCH from '../fetchMethods/FETCH';
import { SignInT } from '../types/user-type';

type props = {
    setIsAuthenticated: (arg0: boolean) => void;
}

function SignIn({setIsAuthenticated} : props){

    const { enqueueSnackbar } = useSnackbar();
    const history = useHistory();
    const formClasses = formStyles();
    const linkClasses = LinkStyle();

    async function handleSubmit(user: SignInT){
        const res = await FETCH({method:'POST', path:`signin`, body:user})
        if(res.token){
            sessionStorage.setItem("token", 'Bearer '+ res.token);
            history.push('/teacherSearcher');
            setIsAuthenticated(true);
        }else{
            enqueueSnackbar(res.message);
        }
    }

    return(
        <Container maxWidth="xs">
            <Grid container className={formClasses.containerForm} >
                <Typography variant="h5" color="initial">Sign In</Typography>
                <Grid item xs={12}>
                    <Formik
                        initialValues = {{email: '', password: '',}}
                        validationSchema={SignInUserS}
                        onSubmit = {(values)=>{
                           handleSubmit(values);
                        }}
                    >
                        <Form className={formClasses.form} >
                            <MyEmailInput label="Correo electronico" name="email" />
                            <MyPasswordInput label="Contrasea" name="password" />
                            <MySubmit value="Enviar" />
                        </Form>
                    </Formik>
                    <Box my={2} >
                        <Link to="/resetPassword" className={linkClasses.link}>
                            <Typography align="center" style={{width: '100%'}} variant="body2">Recupera tu contrasea</Typography>
                        </Link>
                    </Box>
                </Grid>
            </Grid>
        </Container>
    )
}

export default SignIn;