import React, {useState} from 'react';
import { Container, Grid, Typography, IconButton } from '@material-ui/core';
import { Formik, Form } from 'formik';
import SelectSubjects from '../components/SelectSubject';
import MyTextInput from '../components/inputs/MyTextInput';
import SelectTeachers from '../components/SelectTeachers';
import MySubmit from '../components/inputs/MySubmit';
import formStyles from '../material-ui/formStyles';
import AddIcon from '@material-ui/icons/Add';
import RefreshIcon from '@material-ui/icons/Refresh';
import {AddTeacherS} from '../yup-schemas/teacher-schema';
import FETCH from '../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';
import { useHistory } from 'react-router-dom'
import { AddTeacherT } from '../types/teacher-type';

function AddTeacher(){

    const classes = formStyles();
    const [isNewTeacher, setIsNewTeacher] = useState<boolean>(false); 
    const { enqueueSnackbar } = useSnackbar();
    const history = useHistory();

    async function handleSubmit(values: AddTeacherT){
        let res;
        if(isNewTeacher){
            res = await FETCH({method:'POST', path:`user/school/subject/${values.idSubject}/teacher`, body:{teacherName: values.teacherName}})
        }else{
            res = await FETCH({method:'POST', path:`user/school/subject/${values.idSubject}/teacher/${values.idTeacher}`})
        }
        enqueueSnackbar(res.message);
        if(res.status===200){
            history.goBack();
        }
    }

    return(
        <Container component="main" maxWidth="xs">
            <Grid container className={classes.containerForm} >
                <Typography variant="h5" color="initial">Agregar Maestro</Typography> 
                <Grid item xs={12}>
                    <Formik
                        initialValues={{
                            idSubject: '',
                            idTeacher: '',
                            teacherName: '',
                        }}
                        
                        validationSchema={AddTeacherS}
                        onSubmit={(values)=>{
                            handleSubmit(values);
                        }}
                    >
                        <Form className={classes.form}>
                            <AddTeacherField setIsNewTeacher={setIsNewTeacher} isNewTeacher={isNewTeacher} />
                            <SelectSubjects/>
                            <MySubmit value="Enviar" />
                        </Form>
                    </Formik>
                </Grid>
            </Grid>
        </Container>
    )
}

export default AddTeacher


type props = {
    isNewTeacher: boolean,
    setIsNewTeacher: (arg0: boolean) => void
}

function AddTeacherField({isNewTeacher, setIsNewTeacher}: props){
    return(
        <>
        {isNewTeacher?
            <Grid container>
                <Grid item xs={10}>
                    <MyTextInput label="Nombre Profesor" name="teacherName" />
                </Grid>
                <Grid item xs={2}>
                    <IconButton aria-label="addTeacher" onClick={()=>setIsNewTeacher(false)}>
                    <RefreshIcon/>
                    </IconButton>
                </Grid>
            </Grid>
        :
            <Grid container>
                <Grid item xs={10}>
                    <SelectTeachers/>                                
                </Grid>
                <Grid item xs={2}>
                    <IconButton aria-label="addTeacher" onClick={()=>setIsNewTeacher(true)}>
                        <AddIcon/>
                    </IconButton>
                </Grid>  
            </Grid>                            
        }
        </>
    )
}