import React, {useState, useEffect} from 'react';
import GossipAccordion from './GossipAccordion';
import {Grid, Typography, Box} from '@material-ui/core'
import FETCH from '../fetchMethods/FETCH';
import { GossipT } from '../types/gossip-type';

type props = {
    idTeacher?: string
}

function ListGossips({idTeacher}:props){

    const [gossips, setGossips] = useState<GossipT[]>();

    async function getGossips(){
        let res;
        if(idTeacher) res = await FETCH({method:'GET', path:`teacher/${idTeacher}/gossips`})
        else res = await FETCH({method:'GET', path:'gossips'})
        setGossips(res);
    }
    
    useEffect(()=>{
        getGossips();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    return(
        <>
            {gossips? gossips.map((g, i)=>(
                <Grid container spacing={5}>
                    <Grid item xs={12}>
                            <GossipAccordion gossip={g.gossip} teacherName={g.teacherName ? g.teacherName : `Gossip ${i+1}`} />
                    </Grid>
                </Grid>
            )) : 
                <Box mt={3}>
                    <Typography color="textSecondary" align="center" style={{width: '100%'}} variant="h5" >An no hay tendedero</Typography>
                </Box>
            }
        </>
    )
}

export default ListGossips;