import React from 'react';
import {useField} from 'formik';
import { ListItem, FormControlLabel, Checkbox, makeStyles, Theme} from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
    listItem: {
        padding: theme.spacing(0)
    },
}));

type Props = {
    name:string, 
    label:string, 
}

const MyCheckBoxListItem = ({name, label} : Props) =>{
    
    const [field] = useField(name);
    const key = "check"+name;
    const classes = useStyle();

    return(
        <ListItem key={key} dense button className={classes.listItem}>
            <FormControlLabel
                control = {<Checkbox {...field} color="primary"/>}
                label={label}
            />
        </ListItem>
    );
}

export default MyCheckBoxListItem;

