import React, {useState} from 'react';
import {Formik, Form} from 'formik';
import { Typography, List, Grid, Container, FormControl, Box } from '@material-ui/core';
import {CreateQualificationOnlineS} from '../yup-schemas/qualificationOnline-schema';
import MyTextArea from '../components/inputs/MyTextArea';
import MyCheckBoxListItem from '../components/inputs/MyCheckBoxListItem';
import MySubmit from '../components/inputs/MySubmit';
import { InferType } from 'yup';
import FETCH from '../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';
import { useHistory, Link } from 'react-router-dom';
import SelectSubjects from '../components/SelectSubject';
import SelectTeachers from '../components/SelectTeachers';
import linkStyle from '../material-ui/linkStyle';
import formStyles from '../material-ui/formStyles';

type createQualificationOnlineType = InferType<typeof CreateQualificationOnlineS>

export default function QualificationOnlineForm(){
    
    const [teacherID, setTeacherID] = useState<string>()
    const { enqueueSnackbar } = useSnackbar();
    const formClasses = formStyles();
    const classes = linkStyle();
    const history = useHistory();

    async function handleOnSubmit(values: createQualificationOnlineType){
        if(values){
            const res = await FETCH({method:'POST', path:'qualificationOnline', body:values});
            enqueueSnackbar(res.message);
            history.push('/userComments') 
        }
    }
    
    return(
        <Container component="main" maxWidth="xs">
            <Grid container className={formClasses.containerForm}>
                <Typography variant="h5" color="initial">Calificacin Online</Typography> 
                <Grid item xs={12}>
                    <Formik
                        initialValues = {{
                            idTeacher: '',
                            idSubject: '',
                            learn: false,
                            isEasy: false,
                            answer: false,
                            sentDocuments: false,
                            madeVideocalls: false,
                            dissapear: false,
                            beFair: false,
                            comment: '',
                        }}
                        validationSchema = {CreateQualificationOnlineS}
                        onSubmit = {(values) => {
                            handleOnSubmit(values);
                        }}
                    >
                        <Form className={formClasses.form} >
                           
                            <SelectTeachers setIDTeacher={setTeacherID} />
                            <SelectSubjects idTeacher={teacherID} />

                            <FormControl fullWidth>
                                <List>
                                    <MyCheckBoxListItem name="learn" label="Aprendiste?"/>
                                    <MyCheckBoxListItem name="isEasy" label="Es barco?"/>
                                    <MyCheckBoxListItem name="answer" label="Respondi preguntas?"/>
                                    <MyCheckBoxListItem name="sentDocuments" label="Mand documentos/videos?"/>
                                    <MyCheckBoxListItem name="madeVideocalls" label="Hizo videollamadas?"/>
                                    <MyCheckBoxListItem name="dissapear" label="Desapareci?"/>
                                    <MyCheckBoxListItem name="beFair" label="Fue just?"/>
                                </List>
                            </FormControl>
                            <MyTextArea label="Comentario (Opcional)" name="comment"/>
                            <MySubmit value="Enviar" />
                        </Form>
                    </Formik>
                    <Box my={2} >
                        <Link to="/addTeacher" className={classes.link}>
                            <Typography align="center" style={{width: '100%'}} variant="body2">No esta el maestro o no tiene la materia que buscas?</Typography>
                        </Link>
                    </Box>
                </Grid>
            </Grid>
        </Container>
    )
    
}