import React from 'react';
import { Typography, makeStyles, Theme, Box } from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
    text404: {
         background: `linear-gradient(90deg, ${theme.palette.secondary.light} 0%, ${theme.palette.secondary.dark} 100%)`,
         WebkitBackgroundClip: 'text',
         WebkitTextFillColor: 'transparent',
         fontWeight: 'bold',
         fontSize: 200,
         marginTop: theme.spacing(10)
    },
}));

function NotFound(){

    const classes = useStyle();

    return(
        <Box display="flex" flexDirection="column" alignItems="center">
            <Typography display="inline" className={classes.text404} variant="h1" color="initial">404</Typography>
            <Typography display="inline" variant="overline" color="initial">Pagina no Encontrada</Typography>
        </Box>
    )
}


export default NotFound;