import * as yup from 'yup';

export const AddTeacherS = yup.object().shape({
    idSubject: yup.string().required(),
    idTeacher: yup.string().test(
        'required',
        'Elige un maestro',
        function(idTeacher){
            const {teacherName} = this.parent;
            if(!idTeacher && !teacherName){
                return false;
            }
            return true;
        }
    ),
    teacherName: yup.string().test(
        'required',
        'Escribe el nombre',
        function(teacherName){
            const {idTeacher} = this.parent;
            if(!teacherName && !idTeacher){
                return false;
            }
            return true;
        }
    )
})

export const CreateTeacherS = yup.object().shape({
    teacherName: yup.string().required()
})