import React from 'react';
import {Formik, Form} from 'formik';
import {createCareerS} from '../../yup-schemas/career-schema';
import MyTextInput from '../inputs/MyTextInput';
import { CareerT, CreateCareerT } from '../../types/career-type';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type props = {
    career: CareerT,
    getCareers: () => void,
}

function UpdateForm({career, getCareers}: props){

    const { enqueueSnackbar } = useSnackbar();
    const idSubmit = `${career._id}USubmit`

    async function handleSubmit(values: CreateCareerT){
        if(career.careerName!==values.careerName){
            const res = await FETCH({method:'PUT', path:`career/${career._id}`, body:values})
            enqueueSnackbar(res.message);
            if(res.status===200){
                getCareers();
            }
        }
    }

    return(
        <Formik
            initialValues={{careerName: career.careerName}}
            validationSchema={createCareerS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="Career" name="careerName"/>
                <button id={idSubmit} hidden>ENVIAR</button>
            </Form>
        </Formik>
    )
}

export default UpdateForm;