import React, {useState, useEffect} from 'react';
import {TextField} from '@material-ui/core';

type props<T> = {
    data: T[] | undefined,
    setFilteredData: (arg0: T[]) => void,
    optionNameField: Extract<keyof T, string>,
    label: string
}

function SearchBar<T>({data, setFilteredData, optionNameField, label}: props<T>){

    const [searchedW, setSearchedW] = useState<string>('');

    useEffect(()=> {
        if(data){
            if(searchedW!==''){
                    const newData = data.filter(d => ((d[optionNameField] as unknown as string).toLowerCase()).includes(searchedW.toLowerCase()));
                    setFilteredData(newData);
            }else{
                setFilteredData(data);
            }
        }
    }, [searchedW])

    return(
        <TextField
            fullWidth
            variant="outlined"
            label={label}
            onChange={(e) => setSearchedW(e.target.value) }
        />
    )

    


}

export default SearchBar;