import React from 'react';
import {Typography, Box, makeStyles, Theme} from '@material-ui/core';

const useStyle = makeStyles((theme: Theme) => ({
    footer:{
        backgroundColor: theme.palette.primary.dark,
        color: theme.palette.grey[500],
        display: 'fixed',
        bottom: 0
    }
}));

function Footer(){

    const classes = useStyle()

    return(
        <Box className={classes.footer}>
            <Typography display='block' align='center' variant="overline" color="initial">YPZ ~ 2020</Typography>
            <Typography display='block' align='center' variant="overline" color="initial"></Typography>
        </Box>
    )
}

export default Footer;