import React, { useEffect } from 'react';
import FETCH from '../../fetchMethods/FETCH';
import CommentCard from './../commentCard/CommentCard';
import {Grid, Typography} from '@material-ui/core';
import { CommentCardT } from '../../types/comment-type';

type props = {
    teacherID?: string,
    userComments: boolean,
    setTotalComments?: (arg0: number) => void
}

const TeacherCommentsList = ({teacherID, userComments, setTotalComments}: props) => {
    const [comments, setComments] = React.useState<CommentCardT[]>();
    const colors = [
        "hsl(283,60%,40%)",
        "hsl(321.5,60%,40%)",
        "hsl(360,60%,40%)"
    ]
    function getColorRandom(){
        const colorRandom = Math.round(Math.random() * ((colors.length-1)-0));
        return colors[colorRandom];
    }
    
    async function getComments(){
        if(teacherID) getTeacherComments();
        else if(userComments) getUserComments();
    }

    async function getTeacherComments(){
        const res = await FETCH({method:'GET', path:`teacher/${teacherID}/comments`});
        setComments(res);
    }

    async function getUserComments(){
        const res = await FETCH({method:'GET', path:`user/commentsCard`});
        setComments(res);
        if(setTotalComments) setTotalComments(res.length);
    }

    useEffect(() => {
        getComments();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[teacherID])

    return(
        <Grid container spacing={2} >
            {comments && comments.length!==0 ? 
                comments.map((c) => (
                    <Grid item xs={12} key={c._id}>
                        {userComments ? 
                            <CommentCard {...c} color={getColorRandom()} getUserComments={getUserComments}/> 
                        : 
                            <CommentCard {...c} color={getColorRandom()} /> 
                        }
                    </Grid>
                ))
            : 
            <Typography color="textSecondary" align="center" style={{width: '100%'}} variant="h5" >Sin Comentarios</Typography>
            }
        </Grid>
    )
}

export default TeacherCommentsList;



