import React, { useEffect, useState } from 'react';
import MySelect from '../components/inputs/MySelect';
import MyEmailInput from '../components/inputs/MyEmailInput';
import MyPasswordInput from '../components/inputs/MyPasswordInput';
import MyTextInput from '../components/inputs/MyTextInput';
import MySubmit from '../components/inputs/MySubmit';
import {Formik, Form} from 'formik';
import {CreateUserS} from '../yup-schemas/user-schema';
import {Grid, Container, Typography} from '@material-ui/core';
import formStyles from '../material-ui/formStyles';
import { useSnackbar } from 'notistack';
import FETCH from '../fetchMethods/FETCH';
import { useHistory } from 'react-router-dom';
import { CreateUserT } from '../types/user-type';
import { CareerT } from '../types/career-type';
import { SchoolT } from '../types/school-type';

export default function SignUp(){

    const { enqueueSnackbar } = useSnackbar();
    const [careers, setCareers] = useState<CareerT[]>();
    const [schools, setSchools] = useState<SchoolT[]>();
    const [school, setSchool] = useState<string>();
    const classes = formStyles();
    const history = useHistory();
    
    async function handleOnSubmit(user: CreateUserT){
        const res = await FETCH({method:'POST', path:'signup', body:user})
        enqueueSnackbar(res.message);
        if(res.status===200) history.push('/signIn')
    }

    async function getCareers(){
        const resCareers = await FETCH({method:'GET', path:`school/${school}/careers`})
        setCareers(resCareers);
    }

    async function getSchools(){
        const resSchools = await FETCH({method:'GET', path:'schools'})
        setSchools(resSchools);
        setSchool(resSchools[0]._id);
    }

    useEffect(() => {
        if(school){
            getCareers()
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[school])

    useEffect(() => {
        getSchools();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    return(
        <Container  component="main" maxWidth="xs" >
            <Grid container className={classes.containerForm}>
                <Typography variant="h5" color="initial">Sign Up</Typography>
                <Grid item xs={12} >
                    <Formik
                        initialValues={ {   nickName: '', 
                                            idSchool: '',
                                            idCareer: '',
                                            email: '', 
                                            password: '', 
                                            confirmPassword: ''
                                        } }
                        validationSchema={CreateUserS}
                        onSubmit = {(values)=>{
                            handleOnSubmit(values);
                        }}
                    >
                        <Form className={classes.form}  >
                            <MySelect name="idSchool" label="Escuela" id="school" onChange={setSchool}>
                                {schools ? schools.map((s) => (
                                    <option value={s._id} key={s._id} >{s.schoolName}</option>
                                ))
                                :null}
                            </MySelect>
                            <MySelect name="idCareer" label="Carrera" id="career">
                                {careers ? careers.map((c) => (
                                    <option value={c._id} key={c._id} >{c.careerName}</option>
                                ))
                                :null}
                            </MySelect>
                            <MyTextInput name="nickName" label="NickName"/>
                            <MyEmailInput name="email" label="Correo Electronico" />
                            <MyPasswordInput name="password" label="Contrasea" />
                            <MyPasswordInput name="confirmPassword" label="Confirma Contrasea" />
                            <MySubmit value="Registrate"/>
                        </Form>
                    </Formik>
                </Grid>
            </Grid>
        </Container>
    )
               
}
