import React, { useEffect, useState } from 'react';
import {Box, Grid, Typography, Container} from '@material-ui/core'
import MyChart from '../components/teacher/TeacherChart';
import { useParams } from 'react-router-dom';
import TeacherCommentsList from '../components/teacher/TeacherCommentsList';
import TeacherAvatarScored from '../components/teacher/TeacherAvatarScored';
import FETCH from '../fetchMethods/FETCH';
import ListGossips from '../components/ListGossips';

export default function TeacherProfile(){

    const {idTeacher} = useParams<{idTeacher: string}>();

    return(
        <Container>
            <Grid container spacing={2} >
                <Grid item xs={12}>
                    <Box display='flex' alignItems="center" justifyContent='center'>
                        <TeacherAvatarScored idTeacher={idTeacher} /> 
                        <TeacherNameText idTeacher={idTeacher} />
                    </Box>
                </Grid>
                <Grid item xs={12} md={4}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <MyChart idTeacher={idTeacher}/>
                        </Grid>
                        <Grid item xs={12}>
                            <ListGossips idTeacher={idTeacher}/>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid item xs={12} md={8}>
                    <TeacherCommentsList userComments={false} teacherID={idTeacher} />
                </Grid>
            </Grid>
        </Container>
    )
}

type props = {
    idTeacher: string
}

function TeacherNameText({idTeacher}: props){

    const [teacherName, setTeacherName] = useState<string>('');

    async function getTeacherName(){
        const res = await FETCH({method:'GET', path:`teacher/${idTeacher}`});
        setTeacherName(res.teacherName);
    }

    useEffect(() => {
        getTeacherName();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[])

    return(
        <Typography align="center" variant="h4">{teacherName}</Typography>
    )
}