import React, {ReactNode} from 'react';
import { Route } from 'react-router-dom';

type props = {
    children : ReactNode,
    path : string,
    exact : boolean
}

const PublicRoute = ({children, path, exact} : props) => {
    return(
        <>
            <Route exact={exact} path={path}>
                {children}
            </Route>
        </>
    )
}

export default PublicRoute;