import React, { useState, useEffect, ChangeEvent} from 'react';
import { Table, TableContainer, TableBody } from '@material-ui/core';
import DinamicRow from '../adminForms/DinamicRow';
import { SubjectT } from '../../types/subject-type';
import UpdateSubject from './UpdateSubject';
import SearchBar from '../adminForms/SearchBar';
import FETCH from '../../fetchMethods/FETCH';
import {useSnackbar} from 'notistack';

type props = {
    getSubjects: () => void,
    subjects: SubjectT[] | undefined,
    setSubjects: (arg0: SubjectT[] | undefined) => void
}

function Tabla({getSubjects, subjects, setSubjects}: props){

    //fSubjects = filterSubjects
    const [fSubjects, setFSubjects] = useState<SubjectT[] | undefined>(subjects);
    const {enqueueSnackbar} = useSnackbar();    

    useEffect(()=>{
        setFSubjects(subjects);
    },[subjects])

    useEffect(()=>{
        setSubjects(fSubjects);
    },[fSubjects])

    async function deleteSubject(idSubject: string){
        const res = await FETCH({method:'DELETE', path:`subject/${idSubject}`});
        enqueueSnackbar(res.message);
        if(res.status === 200) getSubjects();
    }

    return(
        <>
            <SearchBar data={subjects} optionNameField="subjectName" setFilteredData={setFSubjects} label="Seach subject"/>
            <TableContainer>
                <Table size="small">
                    <TableBody>
                        {fSubjects? fSubjects.map((s: SubjectT) => (
                            <DinamicRow checked={s.checked} setChecked={(e:ChangeEvent<HTMLInputElement>)=>{s.checked=e.target.checked}} updateForm={<UpdateSubject getSubjects={getSubjects} subject={s} />} deleteFunction={()=>deleteSubject(s._id)} btnIdForm={`${s._id}USubmit`} xName={s.subjectName} key={s._id} />
                        )): null}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}





export default Tabla;