import React from 'react';
import { Grid, Typography } from '@material-ui/core';

type props = {
    title: string,
    subtitle: string
}

function Baneer({title, subtitle}:props){

    return(
        <Grid container>
            <Grid item xs={12}>
                <Typography align="center" variant="h4" color="initial">{title}</Typography>
                <Typography align="center" variant="subtitle1" color="initial">{subtitle}</Typography>
            </Grid>
        </Grid>
    )
}

export default Baneer;