import React from 'react';
import {Box, Typography, makeStyles, Theme, Container, Grid} from '@material-ui/core';
import Decalogue from '../components/info/Decalogue';
import Banner from '../components/info/Banner';
import Contact from '../components/info/Contact';
import engranes from '../resources/engranesBlancos.png';
import Footer from '../components/info/Footer';
import TeacherEvaluation from '../components/info/TeacherEvaluation';
import '../material-ui/info.css';
import ExtraCredits from '../components/info/ExtraCredits';

const useStyle = makeStyles((theme: Theme) => ({
    textGradient: {
        background: `linear-gradient(90deg, ${theme.palette.secondary.light} 0%, ${theme.palette.secondary.dark} 100%)`,
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
        fontWeight: 'bold',
        fontSize: 50,
    },
    title: {
        margin: theme.spacing(3,0,3,0),
    },
    container1:{
        background: `linear-gradient(90deg, hsl(283,60%,35%) 0%, hsl(360,60%,35%) 100%)`,
        color: theme.palette.common.white,
        padding: theme.spacing(2),
        display:"flex", 
        alignItems: 'center', 
        justifyContent: 'center'
    },
    container2:{
        background: `linear-gradient(90deg, hsl(360,60%,35%) 0%, hsl(283,60%,35%) 100%)`,
        color: theme.palette.common.white,
        padding: theme.spacing(2),
        display:"flex", 
        alignItems: 'center', 
        justifyContent: 'center'
    },
    img:{
        backgroundSize: 'cover',
        height: '100px'
    }
}));

function Info(){

    const classes = useStyle()

    return(
        <>
            <Container>
                <Grid container spacing={5}>
                    <Grid item xs={12} style={{display: 'block', textAlign: "center"}}>
                        <Box className={classes.title}>
                            <Typography display="inline" className={classes.textGradient} variant="h1" color="initial">Soy Politecnico</Typography>
                        </Box>
                        <Decalogue/>
                    </Grid>
                    <Grid item xs={12} >
                        <Box className={classes.container1} >
                            <img alt="engranes" className={classes.img} src={engranes} />
                            <Banner 
                                title="Unete al equipo"
                                subtitle="Esta pgina es para nosotros, si sabes programar y buscas practicar o crees poder ofrecer algo ms, no lo pienses y envianos un mensaje"
                            />
                        </Box>
                    </Grid>
                    <Grid item xs={12} >
                        <TeacherEvaluation/>
                    </Grid>
                    <Grid item xs={12} >
                        <Box className={classes.container2} >
                            <Banner 
                                title="No encontraste tu escuela?"
                                subtitle="Ayudanos a completar los datos, comunicate con nosotros para obtener acceso al sistema"
                            />
                            <img alt="engranes" className={classes.img} src={engranes} />
                        </Box>
                    </Grid>
                    <Grid item xs={12} >
                        <Contact/>
                    </Grid>
                    <Grid item xs={12}>
                        <ExtraCredits/>
                    </Grid>
                </Grid>
            </Container>
            <Footer/>
        </>
    )
}


export default Info;

