import React from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import MyPasswordInput from '../components/inputs/MyPasswordInput'
import MySubmit from '../components/inputs/MySubmit'
import { Formik, Form } from 'formik';
import formStyles from '../material-ui/formStyles';
import {ResetPasswordS} from '../yup-schemas/resetPassword-schema';
import FETCH from '../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';
import { useHistory, useParams } from 'react-router-dom'
import { ResetPasswordT } from '../types/resetPassword-type';

type props = {
    setIsAuthenticated: (arg0: boolean) => void;
}

function ResetPassword({setIsAuthenticated}: props){
    const {idUser} = useParams<{idUser: string}>();
    const classes = formStyles();
    const { enqueueSnackbar } = useSnackbar();
    const history = useHistory();

    async function handleSubmit(values:ResetPasswordT){
        const res = await FETCH({method:'PUT', path:`user/${idUser}/resetPassword`, body:values});
        enqueueSnackbar(res.message);
        if(res.status===200){
            sessionStorage.removeItem('token');
            history.push('/signIn');
            setIsAuthenticated(false);
        }
    }

    return(
        <Container maxWidth="xs">
            <Grid container className={classes.containerForm}>
                <Typography variant="h5" color="initial">Recuperar Contrasea</Typography>
                <Grid item xs={12}>
                    <Formik
                        initialValues = {{password: '', newPassword: '', confirmPassword:''}}
                        validationSchema={ResetPasswordS}
                        onSubmit = {(values)=>{
                           handleSubmit(values);
                        }}
                    >
                        <Form className={classes.form} >
                            <MyPasswordInput name="password" label="Contrasea" />
                            <MyPasswordInput name="newPassword" label="Nueva Contrasea" />
                            <MyPasswordInput name="confirmPassword" label="Confirma Contrasea" />
                            <MySubmit value="Enviar" />
                        </Form>
                    </Formik>
                </Grid>
            </Grid>
        </Container>
    )
}


export default ResetPassword;