import React from 'react'
import DeleteRoundedIcon from '@material-ui/icons/DeleteRounded';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar, SnackbarKey } from 'notistack';
import { IconButton, Button } from '@material-ui/core';

type props = {
    idComment: string,
    getUserComments: () => void,
} 

function DeleteCommentButton({idComment, getUserComments}: props){

    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    const confirmActions = (key: SnackbarKey) => (
        <>
            <Button autoFocus onClick={() => {handleDelete(key)}}>
                Eliminar
            </Button>
            <Button onClick={() => { closeSnackbar(key) }}>
                Cancelar
            </Button>
        </>
    );

    async function handleDelete(key:SnackbarKey){
        closeSnackbar(key);
        const res = await FETCH({method:'DELETE', path:`user/comment/${idComment}`})
        enqueueSnackbar(res.message);
        if(getUserComments){
            getUserComments()
        }
    }

    function showAlertConfirm(){
        enqueueSnackbar("Seguro? ", {
            variant: 'warning',
            action: confirmActions
        });
    }
    
    return(
        <IconButton aria-label="deleteComment" onClick={showAlertConfirm}>
            <DeleteRoundedIcon  color="primary" />
        </IconButton>
    )
}

export default DeleteCommentButton;