import React, {useEffect, useState} from 'react';
import MySelect from './inputs/MySelect';
import FETCH from '../fetchMethods/FETCH';
import { SubjectT } from '../types/subject-type';

type props = {
    idTeacher?: string
}

function SelectSubjects({idTeacher}: props){

    async function getTeacherSubjects(idTeacher : string){
        const res = await FETCH({method:'GET', path:`user/school/teacher/${idTeacher}/subjects`})
        setSubjects(res);
    }

    async function getSubjects(){
        const res = await FETCH({method:'GET', path:`user/school/subjects`})
        setSubjects(res);
    }

    useEffect(() => {
        if(idTeacher){
            getTeacherSubjects(idTeacher);
        }else{
            getSubjects();
        }
    },[idTeacher])

    const [subjects, setSubjects] = useState<SubjectT[]>()

    return(
        <MySelect id="subject" name="idSubject" label="Materia">
            {subjects? subjects.map((s) => (
                    <option key={s._id} value={s._id}>{s.subjectName}</option>
            )) : null}
        </MySelect>
    )
}

export default SelectSubjects;