import React from 'react';
import {useField} from 'formik';
import {TextField} from '@material-ui/core';

type Props = {
    name:string, 
    label:string, 
}

const MyEmailInput = ({name, label} : Props) => {
    const[field, meta] = useField(name);
    return(
        <> 
            {meta.touched && meta.error 
                ? 
                <TextField fullWidth type="email" label={label} error helperText={meta.error} {...field}/>    
                : 
                <TextField fullWidth type="email" label={label} {...field}/>
            }
        </>
    )
}

export default MyEmailInput;

