import React from 'react';
import { Container, Grid, Typography, Button, Box } from '@material-ui/core';
import {Formik, Form} from 'formik';
import MySubmit from '../components/inputs/MySubmit';
import formStyles from '../material-ui/formStyles';
import {DeleteAccountS} from '../yup-schemas/user-schema';
import { useSnackbar, SnackbarKey } from 'notistack';
import FETCH from '../fetchMethods/FETCH';
import {useHistory} from 'react-router-dom'
import MyPasswordInput from '../components/inputs/MyPasswordInput';

type DeleteAccountType = {
    password: string
}

type props = {
    setIsAuthenticated: (arg0: boolean) => void;
}

function DeleteAccount({setIsAuthenticated}: props){
    const formClasses = formStyles();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const history = useHistory();

    async function deleteUser(key: SnackbarKey, values: DeleteAccountType){
        closeSnackbar(key);
        const res = await FETCH({method:'DELETE', path:'user/deleteAccount', body:values});
        enqueueSnackbar(res.message);
        if(res.status===200){
            sessionStorage.removeItem('token');
            history.push('/signIn');
            setIsAuthenticated(false);
        }
    }

    function handleSubmit(values: DeleteAccountType){
        enqueueSnackbar("Seguro? ", {
            variant: 'error',
            action: (key: SnackbarKey) => (
                <>
                    <Button onClick={() => { deleteUser(key, values) }}>
                        Eliminar
                    </Button>
                    <Button onClick={() => { closeSnackbar(key) }}>
                        Cancelar
                    </Button>
                </>
            )
        });
    }

    return(
        <Container maxWidth="xs">
            <Grid container className={formClasses.containerForm}>
                <Typography variant="h5" color="initial">Eliminar Cuenta</Typography>
                <Grid item xs={12}>
                    <Formik
                        initialValues = {{password: ''}}
                        validationSchema={DeleteAccountS}
                        onSubmit = {(values)=>{
                           handleSubmit(values);
                        }}
                    >
                        <Form className={formClasses.form} >
                            <MyPasswordInput name="password" label="Contrasea" />
                            <MySubmit value="Eliminar Cuenta" />
                        </Form>
                    </Formik>
                    <Box my={2} >
                        <Typography color="textSecondary" align="center" style={{width: '100%'}} variant="body2">Recuerda que al eliminar tu cuenta, borras tambin TODOS los datos relacionados a ella</Typography>
                    </Box>
                </Grid>
            </Grid>
        </Container>
    )
}

export default DeleteAccount;