import React from 'react';
import { Button, makeStyles, Theme } from '@material-ui/core';
import SendRoundedIcon from '@material-ui/icons/SendRounded';

const useStyle = makeStyles((theme: Theme) => ({
    submit: {
        margin: theme.spacing(3,0,0,0),
        borderRadius: theme.spacing(5),
        background: `linear-gradient(90deg, ${theme.palette.secondary.light} 0%, ${theme.palette.secondary.dark} 100%)`,
    },
}));

type Props = {
    value:string, 
}

const MySubmit = ({value} : Props) => {
    const classes = useStyle();
    return(
        <Button type="submit" className={classes.submit} fullWidth endIcon={<SendRoundedIcon/>} variant="contained" color="secondary">{value}</Button>
    )
}


export default MySubmit;