import React, { useState, useEffect, ChangeEvent} from 'react';

import { Table, TableContainer, TableBody } from '@material-ui/core';
import DinamicRow from '../adminForms/DinamicRow';
import { TeacherT } from '../../types/teacher-type';
import UpdateTeacher from './UpdateTeacher';
import SearchBar from '../adminForms/SearchBar';
import {useSnackbar} from 'notistack';
import FETCH from '../../fetchMethods/FETCH';

type props = {
    getTeachers: () => void,
    teachers: TeacherT[] | undefined
}

function Tabla({getTeachers, teachers}: props){
    
    //fTeachers = filterTeachers
    const [fTeachers, setFTeachers] = useState<TeacherT[] | undefined>(teachers);
    const {enqueueSnackbar} = useSnackbar();

    useEffect(()=>{
        setFTeachers(teachers);
    },[teachers])

    async function deleteSubject(idTeacher: string){
        const res = await FETCH({method:'DELETE', path:`teacher/${idTeacher}`});
        enqueueSnackbar(res.message);
        if(res.status === 200) getTeachers();
    }

    return(
        <>
            <SearchBar data={teachers} optionNameField="teacherName" setFilteredData={setFTeachers} label="Seach teacher"/>
            <TableContainer>
                <Table size="small">
                    <TableBody>
                        {fTeachers? fTeachers.map((t: TeacherT) => (
                            <DinamicRow checked={t.checked} setChecked={(e:ChangeEvent<HTMLInputElement>)=>{t.checked=e.target.checked}} updateForm={<UpdateTeacher getTeachers={getTeachers} teacher={t} />} deleteFunction={()=>deleteSubject(t._id)} btnIdForm={`${t._id}USubmit`} xName={t.teacherName} key={t._id} />
                        )): null}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}





export default Tabla;