import React from 'react';
import { Container, makeStyles, Theme, createStyles, Fab } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import {Link} from 'react-router-dom';
import tendederos from '../resources/tendederos.jpg';
import ListGossips from '../components/ListGossips';

const useStyles = makeStyles((theme: Theme)=>
  createStyles({
    fab:{
        position: 'fixed',
        bottom: theme.spacing(2),
        right: theme.spacing(2)
    },
    container:{
        height: '100%',
        top: 0,
        width: '100%',
        position: 'fixed'
    }
  })
)

function Gossips(){

    const classes = useStyles();

    return(
        <>
            <img alt="imageBackgound" className={classes.container} src={tendederos}/>
            <Container>
                <ListGossips/>
                <Link to="addGossip">
                    <Fab color="primary" aria-label="add qualification button" className={classes.fab}>
                        <Add />
                    </Fab>
                </Link>
            </Container>
        </>
    )
}

export default Gossips;