import React from 'react';
import {Typography, makeStyles, Theme, Box} from '@material-ui/core'

const useStyle = makeStyles((theme: Theme) => ({
    link:{
        textDecoration: 'none',
        color: theme.palette.secondary.dark
    }
}));

function ExtraCredits(){

    const classes = useStyle();

    return(
        <Box mb={1}>
            <Typography align="center" display="block" variant="caption" color="initial">
                Donkey Icon made by <a className={classes.link} href="https://www.flaticon.com/authors/freepik" title="Freepik">Freepik</a> from <a className={classes.link} href="https://www.flaticon.com/" title="Flaticon"> www.flaticon.com</a>
            </Typography>
        </Box>
    )
}

export default ExtraCredits;