import React from "react";
import MyAppBar from "./MyAppBar";
import MyDrawer from "./MyDrawer";

type propsNav = {
  isAuthenticated: boolean
  setIsAuthenticated: (arg0: boolean) => void;
}

export default function Navigation( {isAuthenticated, setIsAuthenticated} : propsNav ) {

  const [openDrawer, setOpenDrawer] = React.useState(false);

  function handleOpenDrawer(open: boolean) {
    setOpenDrawer(open)
  }

  return (
    <>
        <MyAppBar isAuthenticated={isAuthenticated} setIsAuthenticated={setIsAuthenticated} handleOpenDrawer={handleOpenDrawer} />
        <MyDrawer openDrawer={openDrawer} handleOpenDrawer={handleOpenDrawer} />
    </>
  );
}

