import React, {useState, useEffect} from 'react';
import { Container, Grid, Typography } from '@material-ui/core';
import SchoolsTable from '../components/school/SchoolsTable';
import AddSchool from '../components/school/AddSchool';
import { SchoolT } from '../types/school-type';
import FETCH from '../fetchMethods/FETCH';

function Schools(){

    const [schools, setSchools] = useState<SchoolT[]>();

    async function getSchools(){
        const res = await FETCH({method:'GET', path:'schools'})
        setSchools(res);
    }

    useEffect(()=> {
        getSchools();
    },[])

    return(
        <Container>
            <Grid container spacing={2}>
                <Grid item xs={12} md={5}>
                    <Typography align="center" variant="h5" color="initial">Schools</Typography>
                    <AddSchool getSchools={getSchools} />
                </Grid>
                <Grid item xs={12} md={7}>
                    <SchoolsTable getSchools={getSchools} schools={schools} />
                </Grid>
            </Grid>
        </Container>
    )
}

export default Schools;