import React from 'react';
import {makeStyles, Theme, Typography, Grid} from '@material-ui/core';
import img1 from '../resources/img1.jpg';
import img2 from '../resources/img2.jpg';
import img3 from '../resources/img3.jpg';
import '../material-ui/info.css';

const useStyle = makeStyles((theme: Theme) => ({
    textGradient: {
        background: `linear-gradient(90deg, ${theme.palette.secondary.light} 0%, ${theme.palette.secondary.dark} 100%)`,
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
        fontWeight: 'bold',
        fontSize: 50,
        borderWidth: '3px',
        borderColor: 'white',
        borderTopStyle: 'solid',
        borderBottomStyle: 'solid',
        padding: theme.spacing(2,0,2,0),
    },
    subtitle: {
      display: 'block',
      textAlign: 'center',
      marginBottom: '50px'
    },
    conte:{
        display:'block',
        textAlign: 'center',
        background: 'rgba(255,255,255,0.6)',
        margin: theme.spacing(2,0,2,0),
    },
    img:{
        backgroundSize: 'cover',
        height: '100%',
        width: '100%',
    },
    cards:{
        position: 'sticky',
        top: 0,
        height:'100vh'
    },
    txtDecalogo:{
        textAlign:'center',
        fontFamily: 'Encode Sans Condensed'
    }
}));

function Home(){

    const classes = useStyle();

    return(
        <Grid container style={{paddingTop:'50px'}} >
            
            <Grid style={{zIndex:100, position: 'sticky', top: '150px'}} className={classes.conte} item xs={12}>
                <Typography display="inline" style={{zIndex:100}} align="center" variant="h1" color="initial" className={classes.textGradient}>Soy Politecnico</Typography>
            </Grid>

            <Grid style={{ zIndex:1}} item xs={12} className={classes.subtitle}>
              <Typography variant="overline" color="initial">Una pagina de los estudiantes, para los estudiantes</Typography>
            </Grid>

            {/*Aqu inician las fotos*/}

            <Grid style={{ zIndex:2}} className={classes.cards} item xs={12}>
                <img alt="imageBackgound-1" className={classes.img} src={img1}/>
            </Grid>

            <Grid style={{ zIndex:3}} className={classes.cards} item xs={12}>
                <img alt="imageBackgound-2" className={classes.img} src={img2}/>
            </Grid>

            <Grid style={{ zIndex:4}} className={classes.cards} item xs={12}>
                <img alt="imageBackgound-3" className={classes.img} src={img3}/>
            </Grid>
            
        </Grid>
    )
}


export default Home;