import React from 'react';
import {useField} from 'formik';
import { TextField} from '@material-ui/core';

type Props = {
    name:string, 
    label:string, 
}

const MyPasswordInput = ({name, label} : Props) => {
    const[field, meta] = useField(name);
    return(
        <> 
            {meta.touched && meta.error 
                ? 
                <TextField fullWidth type="password" label={label} error helperText={meta.error} {...field} />    
                : 
                <TextField fullWidth type="password" label={label} helperText="Minimo 8 caracteres" {...field} />
            }
        </>
    )
}

export default MyPasswordInput;
