import * as yup from 'yup'

export const CreateUserS = yup.object().shape({
    nickName: yup.string().required().max(30),
    idSchool: yup.string().required(),
    idCareer: yup.string().required(),
    email: yup.string().required().email().max(320),
    password: yup.string().required().max(50).min(8),
    confirmPassword: yup.string().required().max(50).min(8).oneOf([yup.ref('password')], 'Passwords must match'),
});

export const DeleteAccountS = yup.object().shape({
    password: yup.string().required().max(50).min(8),
});

export const SignInUserS = yup.object().shape({
    email: yup.string().email().required().max(320),
    password: yup.string().required().max(50).min(8),
});

export const AddRoleS = yup.object().shape({
    idRole: yup.string().required()
})


export const SearchNickNameS = yup.object().shape({
    nickName: yup.string().required().max(30)
})