import React from 'react';
import {Formik, Form} from 'formik'
import MyTextInput from '../inputs/MyTextInput';
import MySubmit from '../inputs/MySubmit';
import {CreateSchoolS} from '../../yup-schemas/school-schema';
import FETCH from '../../fetchMethods/FETCH';
import { useSnackbar } from 'notistack';

type SchoolType = {
    schoolName: string,
}

type props = {
    getSchools: () => void
}

function AddForm({getSchools}: props){

    const { enqueueSnackbar } = useSnackbar();

    async function handleSubmit(values: SchoolType){
        const res = await FETCH({method:'POST', path: 'school', body:values});
        enqueueSnackbar(res.message);
        if(res.status===200) getSchools();
    }

    return(
        <Formik
            initialValues={{schoolName: ''}}
            validationSchema={CreateSchoolS}
            onSubmit={(values)=>{
                handleSubmit(values);
            }}
        >
            <Form>
                <MyTextInput label="School" name="schoolName"/>
                <MySubmit value="save"/>
            </Form>
        </Formik>
    )
}

export default AddForm;