import React from 'react';
import {useField} from 'formik';
import { FormControl, InputLabel, NativeSelect, FormHelperText } from '@material-ui/core';

type Props = {
    name:string, 
    label:string, 
    children: React.ReactNode,
    id: string,
    onChange?: (arg0: string) => void
}

const MySelect = ({name, label, children, id, onChange} : Props) => {
    const [field, meta] = useField(name);
    return(
        <>
            <FormControl fullWidth>
                <InputLabel htmlFor={id}>{label}</InputLabel>
                <NativeSelect {...field} onChange={(e)=>{
                    if(onChange) onChange(e.target.value)
                    field.onChange(e)
                }}>
                    <option aria-label="None" value="" />
                    {children}
                </NativeSelect>
                {meta.touched && meta.error ? <FormHelperText error>{meta.error}</FormHelperText> : null}
            </FormControl>
        </>
    );
}



export default MySelect;

