import React from 'react'
import {Box, Typography} from '@material-ui/core';

function ToolTipInfo({user, createdAt}: {user: string, createdAt: string}){
    return(
        <Box>
            <Typography align="right" variant="body2">{user}</Typography>
            <Typography align="right" variant="body2">{createdAt} </Typography>
        </Box>
    )
}

export default ToolTipInfo;