import React, { useState, useEffect } from 'react';

import { Table, TableContainer, TableBody } from '@material-ui/core';
import DinamicRow from '../adminForms/DinamicRow';
import { SchoolT } from '../../types/school-type';
import UpdateSchool from './UpdateSchool';
import SearchBar from '../adminForms/SearchBar';
import FETCH from '../../fetchMethods/FETCH';
import {useSnackbar} from 'notistack';

type props = {
    getSchools: () => void,
    schools: SchoolT[] | undefined
}

function Tabla({getSchools, schools}: props){

    //fSchools = filterSchools
    const [fSchools, setFSchools] = useState<SchoolT[] | undefined>(schools);
    const {enqueueSnackbar} = useSnackbar();    

    useEffect(()=>{
        setFSchools(schools);
    },[schools])

    async function deleteSchool(idSchool: string){
        const res = await FETCH({method:'DELETE', path:`school/${idSchool}`});
        enqueueSnackbar(res.message);
        if(res.status === 200) getSchools();
    }

    return(
        <>
            <SearchBar data={schools} optionNameField="schoolName" setFilteredData={setFSchools} label="Seach school"/>
            <TableContainer>
                <Table size="small">
                    <TableBody>
                        {fSchools? fSchools.map((s: SchoolT) => (
                            <DinamicRow updateForm={<UpdateSchool getSchools={getSchools} school={s} />} deleteFunction={()=>deleteSchool(s._id)} btnIdForm={`${s._id}USubmit`} xName={s.schoolName} key={s._id} />
                        )): null}
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    )
}





export default Tabla;