import React from 'react'
import { createStyles, makeStyles, Theme, Zoom, useScrollTrigger, Fab } from '@material-ui/core'
import NavigationIcon from '@material-ui/icons/Navigation';

const useStyles = makeStyles((theme: Theme)=>
    createStyles({
        fab:{
            position: 'fixed',
            bottom: theme.spacing(2),
            right: theme.spacing(2),
        }
    })
)
  
function ScrollTopFab() {
    const classes = useStyles();


    const trigger = useScrollTrigger({
        disableHysteresis: true,
        threshold: 100,
    });

    const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
        //back to top anchor id is from the toolbar in app.tsx
        const anchor = ((event.target as HTMLDivElement).ownerDocument || document).querySelector(
                        '#back-to-top-anchor',
                    );
        if (anchor) {
        anchor.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
    };

    return (
        <Zoom in={trigger}>
            <Fab color="primary" onClick={handleClick} className={classes.fab}>
                <NavigationIcon />
            </Fab>
        </Zoom>
    );
}

export default ScrollTopFab;